export const LoanTable = (props) => {

    const theadList = [
        "Pokok",
        "Bunga",
        "Total",
        "Total hutang",
        "Sisa",
    ];
    const thead= [];
    for (let i of theadList.keys()) {
        thead.push(<th key={i} className="right-align">{theadList[i]}</th>);
    };

    const tbody = [];
    for (let item of props.list) {
        const itemProperties = [
            item.pokok,
            item.bunga,
            item.total,
            item.totalHutang,
            item.sisa,
        ];
        const td = [];
        for (let i of itemProperties.keys()) {
            td.push(
                <td key={i} className="right-align">
                    {itemProperties[i].toLocaleString("id-ID")}
                </td>
            );
        }
        tbody.push(
            <tr key={item.key}>
                <td>{item.tenor}</td>
                {td}
            </tr>
        )
    };
    
    return (
        <fieldset style={{ marginTop: "30px"}}>
            <table className="stripped responsive-table">
                <thead>
                    <tr>
                        <th>Tenor</th>
                        {thead}
                    </tr>
                </thead>
                <tbody>
                    {tbody}
                </tbody>
            </table>
        </fieldset>
    );
};