import { useState, useEffect } from "react";

export const CounterFunc = (props) => {
    const [getIncrement, setIncrement] = useState(0);
    const [getDecrement, setDecrement] = useState(0);

    useEffect(() => {
        console.log("componentDidMount");
    }, []);

    useEffect(() => {
        console.log("componentDidUpdate");
    }, [getIncrement, getDecrement]);

    useEffect(() => {
        return () => {
            console.log("componentWillUnmount");
        }
    }, []);

    const onIncrease = () => {
        setIncrement(getIncrement + 1);
    };
    const onDecrease = () => {
        setDecrement(getDecrement + 1);
    };

    return (
        <div>
            <button onClick={onIncrease}>Increment</button>
            <h3>{getIncrement}</h3>
            <button onClick={onDecrease}>Decrement</button>
            <h3>{getDecrement}</h3>
        </div>
    );
};