import { useState, useEffect } from "react";

export const TapAlertFunc = () => {
    const [getCount, setCount] = useState(0);
    const onClick = () => {
        setCount(getCount + 1);
    };
    const longResolve = () => {
        return new Promise((rest) => {
            setTimeout(rest, 5000)
        });
    };

    useEffect(() => {
        longResolve().then(() => {
            console.log("componentDidMount or componentDidUpdate " + getCount);
        });
    }, [getCount]);

    return (
        <div>
            <button onClick={onClick}>
                Count: {getCount}
            </button>
            <p>Refresh, click the button a few times before 5 seconds.</p>
            <p>The output will be shown in the console.log</p>
        </div>
    );
};