import { createSlice } from "@reduxjs/toolkit";

const ADD = "add";
const REMOVE = "remove";

const handleProduct = (state, action, addOrRemove) => {
    const item = action.payload;
    const index = state.items.findIndex (
        it => it.product.id === item.product.id
    );
    const items = [...state.items];
    let totalPrice;
    if (addOrRemove === ADD) {
        totalPrice = state.totalPrice + item.product.price;
    }
    else if (addOrRemove === REMOVE) {
        totalPrice = state.totalPrice - item.product.price;
    }

    if (item && item.quantity > 0) {
        if (index !== -1) {
            items[index] = { ...item };
        }
        else {
            if (addOrRemove === ADD) {
                items.push({ ...item });
            }
            else if (addOrRemove === REMOVE) {
                console.log("Cannot be removed again, item is already removed");
            }
        }
        return { ...state, items: items, totalPrice };
    }
    else {
        if (addOrRemove === REMOVE) {
            const removedItem = items.filter(e => {
                return e.product.id !== item.product.id;
            });
            return { ...state, items: removedItem, totalPrice };
        }
    }
}

export const CartSlice = createSlice({
    name: "cart",
    initialState: {
        items: [],
        totalPrice: 0,
    },
    reducers: {
        addProduct: (state, action) => {
            return handleProduct(state, action, ADD);
        },
        removeProduct: (state, action) => {
            return handleProduct(state, action, REMOVE);
        },
    }
});

export const { addProduct, removeProduct } = CartSlice.actions;
export const selectCartItems = (state) => state.cart.items;
export const selectCartTotalPrice = (state) => state.cart.totalPrice;
export default CartSlice.reducer;