import { Link } from "react-router-dom";
import { CustomerPageList, ProductPageList } from "../../App";

export const ProductTable = (props) => {
    const tbody = props.list.map((product) => {
        return (
            <tr key={product.id}>
                <td>{product.type}</td>
                <td>{product.id}</td>
                <td>{product.name}</td>
                <td>{product.ltv}</td>
                <td>{product.biayaJasaPenyimpanan}</td>
                <td>{product.recStatus}</td>
                <td>
                    <Link to={ProductPageList["Detail Produk"]["path"]} state={{ id: product.id }}>
                        detail
                    </Link>
                </td>
            </tr>
        )
    })
    return (
        <table className="stripped responsive-table">
            <thead>
                <tr>
                    <th>Tipe</th>
                    <th>Kode</th>
                    <th>Nama</th>
                    <th>LTV</th>
                    <th>Biaya Jasa Penyimpanan</th>
                    <th>Status</th>
                    <th>action</th>
                </tr>
            </thead>
            <tbody>
                {tbody}
            </tbody>
        </table>
    );
};