import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { successAlert } from "../../alert/SweetAlert";
import { Api, formatDecimal, replaceNonNumber, responseHandler, toLocaleString_US } from "../../Api";

export const UserAdd = () => {
    const [getName, setName] = useState("");
    const [getHp, setHp] = useState("");
    const [getDesc, setDesc] = useState("");
    const [getTxnLimit, setTxnLimit] = useState("0");
    const [getRegDate, setRegDate] = useState("");

    const [title] = useState("Pengaturan User - Baru");

    const navigate = useNavigate();

    const onClickSave = async () => {
        const details = {
            name: getName,
            hp: getHp,
            desc: getDesc === "" ? null : getDesc,
            txnLimit: getTxnLimit,
            regDate: getRegDate,
        };
        const response = await Api.User.doInsert(details);
        responseHandler(response, () => {
            successAlert("New user has been saved");
            navigate(-1);
        });
    };

    const pengaturanUserBaru = [
        ["User Id",
            true,
            "",
            () => {},
            {}],
        ["Nama User",
            false,
            getName,
            (e) => setName(e.target.value),
            {}],
        ["Nomor Hp",
            false,
            getHp,
            (e) => setHp(replaceNonNumber(e.target.value)),
            {}],
        ["Keterangan",
            false,
            getDesc,
            (e) => setDesc(e.target.value),
            {}],
        ["Maksimal Limit Transaksi User",
            false,
            toLocaleString_US(getTxnLimit),
            (e) => setTxnLimit(formatDecimal(e.target.value)),
            {}],
        ["Tanggal Masuk",
            false,
            getRegDate,
            (e) => setRegDate(e.target.value),
            { type: "date" }],
    ];
    const rowLeftAlign = [];
    for (const i of pengaturanUserBaru.keys()) {
        const currentRow = pengaturanUserBaru[i];

        const name= currentRow[0];
        const properties = {
            className: "col s7 materialize-textarea",
            disabled: currentRow[1],
            value: currentRow[2],
            onChange: currentRow[3],
            ...currentRow[4],
        }

        rowLeftAlign.push(
            <div key={rowLeftAlign.length} className="row left-align">
                <div className="input-field col s12">
                    <h6 className="col s4 pull-s0">{name}</h6>
                    {name === "Keterangan" ? (<textarea {...properties}/>) : (<input {...properties}/>)}
                </div>
            </div>
        );
    }

    const buttonsArray = [
        ["Kembali",
            () => navigate(-1),
            "",
            "arrow_back",
        ],
        ["Simpan",
            onClickSave,
            "green",
            "save",
        ],
    ];
    const materialIconsLeft = [];
    for (const i of buttonsArray.keys()) {
        const currentRow = buttonsArray[i];

        const name = currentRow[0];
        const properties = {
            onClick: currentRow[1],
            className: "btn " + currentRow[2],
        };
        const icon = currentRow[3];

        materialIconsLeft.push(
            <div key={materialIconsLeft.length} className="col s3">
                <button {...properties} >
                    <i className="material-icons left">{icon}</i>
                    {name}
                </button>
            </div>
        );
    }

    return (
        <fieldset className="container valign-wrapper">
            <div className="center container">
                <h4>{title}</h4>
                {rowLeftAlign}
                <div className="row">
                    <div className="col s6" />
                    {materialIconsLeft}
                </div>
            </div>
        </fieldset>
    );
};