export const CicilanDetailTable = (props) => {
    const {
        detailTransaksi,
        alterSelectedNoCicilan,
    } = props;
    const tbody = detailTransaksi.map((cicilan) => {
        const key = cicilan.noTransaksi + cicilan.cicilanKe;
        return (
            <tr key={key}>
                <td>{cicilan.noTransaksi}</td>
                <td>{cicilan.cicilanKe}</td>
                <td>{cicilan.pokok}</td>
                <td>{cicilan.biayaJasaPenyimpanan}</td>
                <td>{cicilan.dendaKeterlambatan}</td>
                <td>{cicilan.totalTagihan}</td>
                <td>{cicilan.statusCicilan}</td>
                <td>{cicilan.tanggalAktifCicilan}</td>
                <td>{cicilan.tanggalBayar}</td>
                <td>
                    {cicilan.statusCicilan === "DIBAYAR" ? null : (
                        <label>
                            <input
                                type="checkbox"
                                onChange={(e) => {
                                    const noCicilan = parseInt(cicilan.cicilanKe);
                                    if (e.target.checked) {
                                        alterSelectedNoCicilan("ADD", noCicilan);
                                    } else {
                                        alterSelectedNoCicilan("DELETE", noCicilan);
                                    }
                                }}
                                id={key}
                                name={key}
                            />
                            <span />
                        </label>
                    )}
                </td>
            </tr>
        )
    })
    return (
        <table className="stripped responsive-table">
            <thead>
                <tr>
                    <th>No Transaksi</th>
                    <th>Cicilan Ke</th>
                    <th>Pokok</th>
                    <th>Biaya Jasa Penyimpanan</th>
                    <th>Denda</th>
                    <th>Total Tagihan</th>
                    <th>Status Cicilan</th>
                    <th>Tanggal Aktif Cicilan</th>
                    <th>Tanggal Bayar</th>
                    <th>Bayar</th>
                </tr>
            </thead>
            <tbody>
                {tbody}
            </tbody>
        </table>
    );
};