import { toLocaleString_US } from "../../Api";

export const DaftarBarangGadaiTable = (props) => {

    const {
        list,
        onClickDelete
    } = props

    const tbody = list.map((barang) => {
        let key = barang.no;
        let no = barang.no;
        if (barang.hasOwnProperty("id")) {
            key = barang.id.idTransaksi + barang.id.noUrut
            no = barang.id.noUrut;
        }
        return (
            <tr key={key}>
                <td>{no}</td>
                <td>{barang.namaBarang}</td>
                <td>{barang.kondisi}</td>
                <td>{barang.jlh}</td>
                <td>{toLocaleString_US(barang.hargaPerSatuan)}</td>
                <td>{toLocaleString_US(barang.jlh * barang.hargaPerSatuan)}</td>
                {onClickDelete === null ? null : (
                    <td>
                        <button onClick={() => {onClickDelete(no)}} className="btn red">
                            <i className="material-icons">delete</i>
                        </button>
                    </td>
                )}
            </tr>
        )
    })
    return (
        <table className="stripped responsive-table">
            <thead>
                <tr>
                    <th>No</th>
                    <th>Nama Barang</th>
                    <th>Kondisi</th>
                    <th>Jumlah Barang</th>
                    <th>Harga per Satuan</th>
                    <th>Total</th>
                    {onClickDelete === null ? null : <th>action</th>}
                </tr>
            </thead>
            <tbody>
                {tbody}
            </tbody>
        </table>
    );
};