import { useState } from "react";
import { ClockFunc } from "../../components/ClockFunc/index";
import { ClockToggle } from "../../components/ClockToggle/index";
import { TapAlertFunc } from "../../components/TapAlertFunc/index";
import { CounterFunc } from "../../components/CounterFunc/index";

export const Codelab = () => {

    const views = [
        ["CounterFunc", <CounterFunc />],
        ["ClockFunc", <ClockToggle children={<ClockFunc />} />],
        ["TapAlertFunc", <TapAlertFunc />],
    ];

    const [getShow, setShow] = useState(views[0][0]);

    const buttons = [];
    for (let i of views.keys()) {
        buttons.push(<button key={i} onClick={() => setShow(views[i][0])}>{views[i][0]}</button>);
    }

    const content = () => {
        for (let i of views) {
            if (getShow === i[0]) {
                return i[1];
            }
        }
    };

    return (
        <div>
            {buttons}
            <fieldset>
                {content()}
            </fieldset>
        </div>
    );

    
};