import { serverUrl } from "config";
import axios from "../../node_modules/axios/index";
import { errorAlert, errorAlertWithImages } from "../alert/SweetAlert";

export const toLocaleString_US = (number) => {
    let text = number.toString().split(".");
    if (text.length === 2) {
        return text[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",") + "." + text[1]
    } else if (text.length === 1) {
        return text[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    } else {
        throw new Error("Not a valid decimal number");
    }
};

export const formatDecimal = (number) => {
    return number.replace(/[^.\d]/g, '').replace(/^(\d*\.?)|(\d*)\.?/g, "$1$2");
};

export const replaceNonNumber = (number) => {
    return number.replace(/\D/g,'');
};

export const currentPage = (searchParameter) => {
    let page = searchParameter.get("page");
    if ([null, undefined, "", "0"].includes(page)) {
        page = "1";
    }
    return page;
};

const SERVERUNREACHABLE = "CannotReachServer";

const apiLocation = async (folder) => {
    const url = await serverUrl();
    if ([undefined, null].includes(url)) {
        return url;
    }
    return url + "api/" + folder
};
export const responseHandler = (response, functions) => {
    const result = ([undefined, null].includes(response) ? {code: 0} : response.data);
    if (result !== SERVERUNREACHABLE) {
        if (result.code === 200) {
            functions();
        } else if (result.code === 0) {
            
        } else {
            if (typeof result.msg !== 'string') {
                result.msg = Object.values(result.msg).join("<br>");
            }
            errorAlertWithImages(
                result.code + " " + result.description,
                result.msg,
                require("../error/" + result.code + ".jpg"),
            );
        }
    }
    else {
        errorAlert(
            response.code,
            response.message,
        );
    }
};

const METHOD = {
    GET: "get",
    POST: "post",
    PUT: "put",
    DELETE: "delete",
};
const request = async (method, url, folder, data) => {
    if ([undefined, null].includes(url)) {
        
    } else if (!Object.values(METHOD).includes(method)) {
        throw new Error("Invalid axios method!");
    } else {
        url = url + folder;
        const config = {
            method,
            maxBodyLength: Infinity,
            url,
            headers: {
                'Content-Type': 'application/json',
            },
        };
        if (data !== null) {
            config.data = data;
        }
        try {
            const response = await axios.request(config);
            return response;
        }
        catch (error) {
            const axiosError = {
                data: SERVERUNREACHABLE,
                code: error.code,
                message: error.message,
            };
            return axiosError;
        }
    }
};

const URL = {
    USER: apiLocation("user/"),
    CUSTOMER: apiLocation("pelanggan/"),
    PRODUCT: apiLocation("produk/"),
    TRANSACTION: apiLocation("transaksi/"),
    CICILAN: apiLocation("cicilan/"),
};

