import { useEffect, useState } from "react";
import { useLocation, useNavigate } from "../../../node_modules/react-router-dom/dist/index";
import Swal from "sweetalert2";
import { errorAlertThenCloseWindow, successAlert } from "../../alert/SweetAlert";
import { Api, formatDecimal, replaceNonNumber, responseHandler, toLocaleString_US } from "../../Api/index";

export const UserDetail = () => {
    const location = useLocation();
    const locationState = (location.state || {});
    const id = locationState.id;

    const [getName, setName] = useState("");
    const [getHp, setHp] = useState("");
    const [getDesc, setDesc] = useState("");
    const [getTxnLimit, setTxnLimit] = useState("0");
    const [getRegDate, setRegDate] = useState();
    const [getCreatedDate, setCreatedDate] = useState();
    const [getCreatedBy, setCreatedBy] = useState();
    const [getUpdatedDate, setUpdatedDate] = useState();
    const [getUpdatedBy, setUpdatedBy] = useState();
    const [getRecStatus, setRecStatus] = useState();
    const [getIsEdit, setIsEdit] = useState(false);
    const [getTitle, setTitle] = useState("Pengaturan User - Detail");

    const navigate = useNavigate();

    const onClickEdit = () => {
        setTitle("Pengaturan User - Ubah");
        setIsEdit(true)
    };

    const onClickDelete = () => {
        Swal.fire({
            title: "Do you want to delete this user?",
            showDenyButton: true,
            confirmButtonText: "Yes",
            denyButtonText: "No",
        }).then(async (result) => {
            if (result.isConfirmed) {
                const response = await Api.User.doDelete({
                    id: id,  
                    actorId: 0,
                });
                responseHandler(response, () => {
                    successAlert("User has been deleted");
                    navigate(-1);
                });
            }
            else if (result.isDenied) {
                Swal.fire("User is not removed", "", "info");
            };
        });
    };
    const onClickSave = async () => {
        Swal.fire({
            title: "Do you want to save the changes?",
            showDenyButton: true,
            confirmButtonText: "Yes",
            denyButtonText: "No",
        }).then(async (result) => {
            if (result.isConfirmed) {
                const response = await Api.User.doUpdate({
                    id: id,
                    name: getName,
                    hp: getHp,
                    desc: getDesc,
                    txnLimit: getTxnLimit,
                    regDate: getRegDate,
                });
                responseHandler(response, () => {
                    successAlert("User has been updated");
                });
            }
            else if (result.isDenied) {
                Swal.fire("Changes are not saved", "", "info");
            };
        });
    };
    const getDetail = async () => {
        const response = await Api.User.doGetDetail(id);
        responseHandler(response, () => {
            const details = response.data.data;
            setName(details.name);
            setHp(details.hp);
            setDesc(details.desc || "");
            setTxnLimit(details.txnLimit);
            setRegDate(details.regDate);
            setCreatedDate(details.createdDate);
            setCreatedBy(details.creatorId);
            setUpdatedDate(details.updatedDate);
            setUpdatedBy(details.updaterId);
            setRecStatus(details.recStatus);
        });
    };

    useEffect(() => {
        if (id === undefined) {
            errorAlertThenCloseWindow("Error", "No User ID detected!");
        } else {
            getDetail();
        }
    }, []);

    const pengaturanUserDetail = [
        ["User Id",
            true,
            id,
            () => {},
            {}],
        ["Nama User",
            !getIsEdit,
            getName,
            (e) => setName(e.target.value),
            {}],
        ["Nomor Hp",
            !getIsEdit,
            getHp,
            (e) => setHp(replaceNonNumber(e.target.value)),
            {}],
        ["Keterangan",
            !getIsEdit,
            getDesc,
            (e) => setDesc(e.target.value),
            {}],
        ["Maksimal Limit Transaksi User",
            !getIsEdit,
            toLocaleString_US(getTxnLimit),
            (e) => setTxnLimit(formatDecimal(e.target.value)),
            {}],
        ["Tanggal Masuk",
            true,
            new Date(getRegDate).toLocaleDateString("sv-SE"),
            () => {},
            {}],
        ["Tanggal Buat / Oleh",
            true,
            getCreatedBy ? `${new Date(getCreatedDate).toLocaleDateString("sv-SE")} / ${getCreatedBy}` : "-",
            () => {},
            {}],
        ["Tanggal Update Terakhir / Oleh",
            true,
            getUpdatedBy ? `${new Date(getUpdatedDate).toLocaleDateString("sv-SE")} / ${getUpdatedBy}` : "-",
            () => {},
            {}],
    ];
    const rowLeftAlign = [];
    for (let i of pengaturanUserDetail.keys()) {
        const currentRow = pengaturanUserDetail[i];

        const name = currentRow[0];
        const properties = {
            className: "col s7 materialize-textarea",
            disabled: currentRow[1],
            value: currentRow[2],
            onChange: currentRow[3],
            ...currentRow[4],
        };

        rowLeftAlign.push(
            <div key={rowLeftAlign.length} className="row left-align">
                <div className="input-field col s12">
                    <h6 className="col s4 pull-s0">{name}</h6>
                    {name === "Keterangan" ? (<textarea {...properties}/>) : (<input {...properties}/>)}
                </div>
            </div>
        );
    }

    let buttonsArray = [
        ["Kembali",
            () => navigate(-1),
            "",
            false,
            "arrow_back",
        ],
    ];
    if (!getIsEdit) {
        buttonsArray = buttonsArray.concat([
            ["Hapus",
                onClickDelete,
                "red",
                getRecStatus === "N",
                "delete",
            ],
            ["Ubah",
                onClickEdit,
                "green",
                getRecStatus === "N",
                "edit",
            ],
        ]);
    }
    else {
        buttonsArray = buttonsArray.concat([
            ["Simpan",
                onClickSave,
                "green",
                false,
                "save",
            ],
        ]);
    }
    const materialIconsLeft = [];
    for (let i of buttonsArray.keys()) {
        const currentRow = buttonsArray[i];

        const name =currentRow[0];
        const properties = {
            onClick: currentRow[1],
            className: "btn " + currentRow[2],
            disabled: currentRow[3],
        };
        const icon =currentRow[4];

        materialIconsLeft.push(
            <div key={materialIconsLeft.length} className="col s3">
                <button {...properties}>
                    <i className="material-icons left">{icon}</i>
                    {name}
                </button>
            </div>
        );
    }
    return (
        <fieldset className="container valign-wrapper">
            <div className="center container">
                <h4>{getTitle}</h4>
                {rowLeftAlign}
                <div className="row">
                    <div className={!getIsEdit ? "col s3" : "col s6"} />
                    {materialIconsLeft}
                </div>
            </div>
        </fieldset>
    );
};