export const CariDataPelangganForm = (props) => {
    const {
        idPelanggan,
        hasilPencarian,
        namaPelanggan,
        noKtp,
        noHp,

        onChangeIdPelanggan,
        onChangeHasilPencarian,
        onChangeNamaPelanggan,
        onChangeNoKtp,
        onChangeNoHp,

        onClickSelect,
        onClickSearch,
        onClickEmpty,
    } = props;

    const inputs = [
        ["ID Pelanggan",
            "idPelanggan",
            idPelanggan,
            onChangeIdPelanggan,
        ],
        ["Nama Pelanggan",
            "namaPelanggan",
            namaPelanggan,
            onChangeNamaPelanggan,
        ],
        ["Nomor KTP",
            "nomorKtp",
            noKtp,
            onChangeNoKtp,
        ],
        ["Nomor HP",
            "nomorHp",
            noHp,
            onChangeNoHp,
        ],
        ["Hasil Pencarian",
            "hasilPencarian",
            hasilPencarian,
            onChangeHasilPencarian,
        ],
    ];
    
    const rows = [];
    const rows2 = [];
    for (let i of inputs.keys()) {
        const currentRow = inputs[i];
        const key = i;

        const inputName = currentRow[0];
        const inputId = currentRow[1];
        const value = currentRow[2];
        const onChange = currentRow[3];

        const tablePelanggan = [];
        if (inputId === "hasilPencarian") {
            tablePelanggan.push(<thead key={"tablePelanggan"}><tr>
                <th>No</th>
                <th>KTP</th>
                <th>HP</th>
                <th>Nama</th>
                <th>action</th>
            </tr></thead>);
            for (const k of value.keys()) {
                const pelangganString = value[k];
                if (pelangganString !== "...") {
                    const pelanggan = pelangganString.substring(1, pelangganString.length-1).split("} - {");
                    const no = pelanggan[0];
                    const ktp = pelanggan[1];
                    const hp = pelanggan[2];
                    const nama = pelanggan[3];
                    tablePelanggan.push(<tbody key={no}><tr>
                        <td>{no}</td>
                        <td>{ktp}</td>
                        <td>{hp}</td>
                        <td>{nama}</td>
                        <td>
                            <button onClick={() => {onClickSelect(no)}} className="btn green">
                                <i className="material-icons">add</i>
                            </button>
                        </td>
                    </tr></tbody>);
                } else {
                    tablePelanggan.push(<tfoot key={pelangganString}><tr>
                        <td>{pelangganString}</td>
                    </tr></tfoot>);
                }
            }
        }
        const properties = {
            id: inputId,
            className: "col s6",
            value: value,
            onChange: onChange,
        };
        if (inputId !== "hasilPencarian") {
            rows.push(
                <div key={key} className="col s6">
                    <h6 className="col s6">{inputName}</h6>
                    <input {...properties}/>
                </div>
            );
        } else {
            rows2.push(
                <div key={key} className="s6">
                    <table>{tablePelanggan}</table>
                </div>
            );
        }
    }

    const fieldset = [];
    fieldset.push(
        <div key={fieldset.length} className="row">
            <div >
                {rows}
            </div>
            <div>
                {rows2}
            </div>
        </div>
    );
    fieldset.push(
        <div key={fieldset.length} className="row">
            <div className="col s4" />
            <div className="col">
                <button
                    className="btn"
                    onClick={onClickSearch}
                >
                    <i className="material-icons left">search</i>Cari
                </button>
            </div>
            <div className="col">
                <button
                    className="btn grey"
                    onClick={onClickEmpty}
                >
                    <i className="material-icons left">delete</i>Kosongkan
                </button>
            </div>
        </div>
    );
    return (
        <>
            <div className="row">
                <h5>Cari Data Pelanggan</h5>
                <div className="divider black" />
            </div>
            {fieldset}
        </>
    );
};