import { useEffect, useState } from "react";

export const ClockFunc = () => {
    const [getTime, setTime] = useState(new Date().toLocaleTimeString());

    const tick = () => {
        console.log("tick");
        setTime(new Date().toLocaleTimeString());
    };

    useEffect(() => {
        console.log("clock componentDidMount / componentDidUpdate (time): " + getTime);
        const timerID = setInterval(() => tick(), 1000);
        return () => {
            console.log("clock componentWillUnmount: " + getTime);
            clearInterval(timerID);
        };
    }, [getTime]);

    return (
        <div>
            <h1>Hello, world!</h1>
            <h2>It is {getTime}</h2>
        </div>
    );
};