import { Link } from "react-router-dom";
import { UserPageList } from "../../App";

export const UserTable = (props) => {
    const tbody = props.list.map((user) => {
        return (
            <tr key={user.id}>
                <td>{user.id}</td>
                <td>{user.name}</td>
                <td>Rp.&nbsp;{user.txnLimit.toLocaleString("id-ID")}</td>
                <td>{user.recStatus}</td>
                <td>
                    <Link to={UserPageList["Detail User"]["path"]} state={{ id: user.id }}>
                        detail
                    </Link>
                </td>
            </tr>
        )
    })
    return (
        <table className="stripped responsive-table">
            <thead>
                <tr>
                    <th>User Id</th>
                    <th>Username</th>
                    <th>Limit transaksi</th>
                    <th>Status</th>
                    <th>action</th>
                </tr>
            </thead>
            <tbody>
                {tbody}
            </tbody>
        </table>
    );
};