import './index.css';

export const Pagination = (props) => {

    const {
        maxItems,
        totalPages,
        page,
        itemNumbers,
        onClick,
    } = props;
    
    let pageNumber;
    if (!(/^\d+$/.test(page))) {
        pageNumber = 1;
    } else {
        pageNumber = parseInt(page);
    }

    const before = pageNumber - 1;
    const after = pageNumber + 1;

    const buttons = [];
    const goClassTo = (to, className, text) => {
        buttons.push(<button onClick={() => onClick(to)} className={className} key={buttons.length}>{text}</button>);
    };
    const goTo = (to) => {
        goClassTo(to, "", to);
    };

    goClassTo("1", "scrolling", <>&laquo;</>);
    if (itemNumbers !== 0) {
        if (before !== 0) {
            if (pageNumber === totalPages && (before - 3 >= 0)) {
                if (pageNumber !== 4) {
                    goTo((before - 3));
                }
                goTo((before - 2));
            }
            if (pageNumber === totalPages - 1 && (before - 2 > 0)) {
                goTo((before - 2));
            }
            if (before >= 2) {
                goTo((before - 1));
            }
            goTo(before);
        }
        goClassTo(pageNumber, "active", pageNumber);
        if (itemNumbers > maxItems) {
            goTo(after, after);
            if (itemNumbers > (maxItems*2)) {
                goTo((after + 1));
            }
            if (pageNumber === 1 && totalPages > 3) {
                goTo((after + 2));
                if (totalPages > 4) {
                    goTo((after + 3));
                }
            }
            if (pageNumber === 2 && totalPages > 4) {
                goTo((after + 2));
            }
        }
    }
    goClassTo(totalPages, "scrolling", <>&raquo;</>);

    return (
        <div className="center">
            <div className="pagination">
                {buttons}
            </div>
        </div>
    );
};