import { toLocaleString_US } from "../../Api";

export const DataKontrakForm = (props) => {
    const {
        totalNilaiTaksiran,
        ltv,
        maksimalNilaiPinjaman,
        nilaiPencarianPelanggan,
        biayaAdminBuka,
        diskonAdminBuka,
        biayaAdminBukaAkhir,
        totalNilaiPinjaman,
        tanggalTransaksi,
        tanggalJatuhTempo,
        biayaJasaPenyimpanan,
        biayaJasaPenyimpananPeriode,
        biayaJasaPenyimpananPerPeriode,
        totalBiayaJasaPenyimpanan,
        biayaAdminTutup,
        totalPengembalian,
        
        onChangeDiskonAdminBuka,
        onChangeNilaiPencarianPelanggan,

        nilaiPencarianPelanggan_diskonAdminBuka_editable
    } = props;
    

    const inputs = [
        [
            ["Total Nilai Taksiran",
                "col s6 input-field",
                "totalNilaiTaksiran",
                toLocaleString_US(totalNilaiTaksiran),
            ],
            ["Tanggal Transaksi",
                "col s6 input-field",
                "status",
                tanggalTransaksi,
            ],
        ],
        [
            ["Tanggal Jatuh Tempo",
                "col s6 input-field",
                "tanggalJatuhTempo",
                tanggalJatuhTempo,
            ],
            ["LTV",
                "col s3 input-field",
                "ltv",
                ltv,
            ],
        ],
        [
            ["Maksimal Nilai Pinjaman",
                "col s6 input-field",
                "maksimalNilaiPinjaman",
                toLocaleString_US(maksimalNilaiPinjaman),
            ],
            ["Biaya Jasa Penyimpanan",
                "col s3 input-field",
                "biayaJasaPenyimpanan",
                biayaJasaPenyimpanan,
            ],
            ["per",
                "col s2 input-field",
                "test",
                biayaJasaPenyimpananPeriode,
            ],
        ],
        [
            ["Nilai Pencarian Pelanggan",
                "col s6 input-field",
                "nilaiPencarianPelanggan",
                toLocaleString_US(nilaiPencarianPelanggan),
            ],
            ["Biaya Jasa Penyimpanan / Periode",
                "col s6 input-field",
                "biayaJasaPenyimpananPerPeriode",
                toLocaleString_US(biayaJasaPenyimpananPerPeriode),
            ],
        ],
        [
            ["Biaya Admin Buka",
                "col s6 input-field",
                "biayaAdminBuka",
                toLocaleString_US(biayaAdminBuka),
            ],
            ["Total Biaya Jasa Penyimpanan",
                "col s6 input-field",
                "totalBiayaJasaPenyimpanan",
                toLocaleString_US(totalBiayaJasaPenyimpanan),
            ],
        ],
        [
            ["Biaya Admin Tutup",
                "col s6 input-field",
                "biayaAdminTutup",
                toLocaleString_US(biayaAdminTutup),
            ],
            ["Diskon Admin Buka",
                "col s3 input-field",
                "diskonAdminBuka",
                diskonAdminBuka,
            ],
        ],
        [
            ["Biaya Admin Buka Akhir",
                "col s6 input-field",
                "biayaAdminBukaAkhir",
                toLocaleString_US(biayaAdminBukaAkhir),
            ],
            ["Total Pengembalian",
                "col s6 input-field",
                "totalPengembalian",
                toLocaleString_US(totalPengembalian),
            ],
        ],
        [
            ["Total Nilai Pinjaman",
                "col s6 input-field",
                "totalNilaiPinjaman",
                toLocaleString_US(totalNilaiPinjaman),
            ],
        ],
    ];
    
    const rows = [];
    for (let i of inputs.keys()) {
        const tempArray = [];
        for (let j of inputs[i].keys()) {
            const currentRow = inputs[i][j];
            const key = i.toString() + j.toString();

            let inputName = currentRow[0];
            const className = currentRow[1];
            const inputId = currentRow[2];
            const value = currentRow[3].toString();
            let disabled = true;
            let onChange = () => {};

            if (["diskonAdminBuka", "nilaiPencarianPelanggan"].includes(inputId)) {
                disabled = !nilaiPencarianPelanggan_diskonAdminBuka_editable;
                if (disabled === false) {
                    inputName = <b>{inputName}</b>;
                    if (inputId === "diskonAdminBuka") {
                        onChange = onChangeDiskonAdminBuka;
                    } else {
                        onChange = onChangeNilaiPencarianPelanggan;
                    }
                }
            }
            tempArray.push(
                <div key={key} className={className}>
                    <h6 className="col s12 pull-s0">{inputName}</h6>
                    <input
                        id={inputId}
                        className="validate"
                        value={value}
                        type="text"
                        disabled={disabled}
                        onChange={onChange}
                    />
                </div>
            );
        }
        rows.push(tempArray);
    }

    const fieldset = [];
    for (let i of rows.keys()) {
        fieldset.push(
            <div key={i} className="row">
                {rows[i]}
            </div>
        );
    }
    
    return (
        <>
            <div className="row">
                <h5>Data Kontrak</h5>
                <div className="divider black" />
            </div>
            {fieldset}
        </>
    );
};