import { NavBar } from '../components/NavBar/index';
import { BrowserRouter, Route, Routes } from '../../node_modules/react-router-dom/dist/index';
import { Codelab } from "../pages/CodeLab/index";
import { Customer } from "../pages/Customer/index";
import { CustomerAdd } from "../pages/Customer/CustomerAdd";
import { CustomerDetail } from "../pages/Customer/CustomerDetail";
import { Home } from "../pages/Home/index";
import { LoanForm } from "../pages/LoanForm/index";
import { MarketPlace } from "../pages/MarketPlace/index";
import { Product } from "../pages/Product/index";
import { ProductAdd } from "../pages/Product/ProductAdd";
import { ProductDetail } from "../pages/Product/ProductDetail";
import { ShoppingCart } from "../pages/ShoppingCart/index";
import { Transaction } from "../pages/Transaction/index";
import { TransactionDetail } from "../pages/Transaction/TransactionDetail";
import { User } from "../pages/User/index";
import { UserAdd } from "../pages/User/UserAdd";
import { UserDetail } from "../pages/User/UserDetail";
import { TransactionAdd } from "../pages/Transaction/TransactionAdd";
import { CicilanDetail } from "../pages/Cicilan/CicilanDetail";
import { Cicilan } from "../pages/Cicilan/index";

const objectIteratorPages = [];
const ObjectIterator = (array) => {
    const object = {};
    for (let i of array.keys()) {
        const currentRow = array[i];
        object[currentRow[0]] = {
            "path": currentRow[1],
            "element": currentRow[2],
        };
    }
    objectIteratorPages.push(object);
    return object;
};

const PageListArray = [
    ["Home", "/", <Home />],
    ["Code Lab", "/code-lab", <Codelab />],
    ["Marketplace", "/marketplace", <MarketPlace />],
    ["Shopping Cart", "/shopping-cart", <ShoppingCart />],
    ["Loan Form", "/loan-form", <LoanForm />],
    ["User", "/user", <User />],
    ["Pelanggan", "/pelanggan", <Customer />],
    ["Produk", "/produk", <Product />],
    ["Transaksi", "/transaksi", <Transaction />],
    ["Cicilan", "/cicilan", <Cicilan />],
]; export const PageList = ObjectIterator(PageListArray);

const UserPageListArray = [
    ["User Baru", PageList["User"]["path"] + "/add", <UserAdd />],
    ["Detail User", PageList["User"]["path"] + "/detail", <UserDetail />],
]; export const UserPageList = ObjectIterator(UserPageListArray);

const CustomerPageListArray = [
    ["Pelanggan Baru", PageList["Pelanggan"]["path"] + "/add", <CustomerAdd />],
    ["Detail Pelanggan", PageList["Pelanggan"]["path"] + "/detail", <CustomerDetail />],
]; export const CustomerPageList = ObjectIterator(CustomerPageListArray);

const ProductPageListArray = [
    ["Produk Baru", PageList["Produk"]["path"] + "/add", <ProductAdd />],
    ["Detail Produk", PageList["Produk"]["path"] + "/detail", <ProductDetail />],
]; export const ProductPageList = ObjectIterator(ProductPageListArray);

const TransactionPageListArray = [
    ["Transaksi Baru", PageList["Transaksi"]["path"] + "/add", <TransactionAdd />],
    ["Detail Transaksi", PageList["Transaksi"]["path"] + "/detail", <TransactionDetail />],
]; export const TransactionPageList = ObjectIterator(TransactionPageListArray);

const CicilanPageListArray = [
    ["Detail Cicilan", PageList["Cicilan"]["path"] + "/detail", <CicilanDetail />],
]; export const CicilanPageList = ObjectIterator(CicilanPageListArray);

export const App = () => {
  const routes = [];
  for (const i of objectIteratorPages.keys()) {
    for (const key in objectIteratorPages[i]) {
      routes.push(
        <Route
          key={routes.length}
          path={objectIteratorPages[i][key]["path"]}
          element={objectIteratorPages[i][key]["element"]}
        />
      );
    }
  }
  return (
    <BrowserRouter>
      <NavBar />
      <Routes>{routes}</Routes>
    </BrowserRouter>
  );
};