import { useEffect, useState } from "react";
import { useLocation, useNavigate } from "../../../node_modules/react-router-dom/dist/index";
import { errorAlertThenCloseWindow, successAlert } from "../../alert/SweetAlert";
import { Api, responseHandler } from "../../Api/index";
import { CicilanDetailForm } from "./CicilanDetailForm";
import { CicilanDetailTable } from "./CicilanDetailTable";

export const CicilanDetail = () => {
    const location = useLocation();
    const locationState = (location.state || {});
    const id = locationState.id;

    const [getNoPembayaran, setNoPembayaran] = useState("");
    const [getProdukTransaksi, setProdukTransaksi] = useState("");
    const [getPelanggan, setPelanggan] = useState("");
    const [getNamaProduk, setNamaProduk] = useState("");
    const [getTanggalTransaksi, setTanggalTransaksi] = useState("");
    const [getKeteranganProduk, setKeteranganProduk] = useState("");
    const [getNoTransaksi, setNoTransaksi] = useState("");
    const [getTotalKewajiban, setTotalKewajiban] = useState("");
    const [getTotalNilaiPinjaman, setTotalNilaiPinjaman] = useState("");
    const [getTotalDenda, setTotalDenda] = useState("");
    const [getTenor, setTenor] = useState("");
    const [getTotalPembayaran, setTotalPembayaran] = useState("");
    const [getTanggalJatuhTempo, setTanggalJatuhTempo] = useState("");
    const [getSisaKewajiban, setSisaKewajiban] = useState("");
    const [getDetailTransaksi, setDetailTransaksi] = useState([]);

    const [getTotalPembayaranCicilan, setTotalPembayaranCicilan] = useState("");
    const [getTotalPembayaranDenda, setTotalPembayaranDenda] = useState("");
    const [getBiayaAdminTutup, setBiayaAdminTutup] = useState("");
    const [getDiskon, setDiskon] = useState("");
    const [getTotalTagihan, setTotalTagihan] = useState("");
    const [getPembulatan, setPembulatan] = useState("");
    const [getTotalTagihanSetelahPembulatan, setTotalTagihanSetelahPembulatan] = useState("");
    const [getJumlahInputPembayaran, setJumlahInputPembayaran] = useState("");
    const [getKembalian, setKembalian] = useState("");
    const [getMetodeBayar, setMetodeBayar] = useState("CASH");

    const [getSelectedNoCicilan, setSelectedNoCicilan] = useState([]);
    const [getSelectedDaftarCicilan, setSelectedDaftarCicilan] = useState([]);

    const [getIsEdit, setIsEdit] = useState(true);

    const navigate = useNavigate();

    const onClickHitung = async () => {
        const response = await Api.Cicilan.doHitungTagihan({
            "noTransaksi": getNoTransaksi,
            "metodeBayar": getMetodeBayar,
            "diskon": getDiskon,
            "jumlahInputPembayaran": getJumlahInputPembayaran,
            "selectedNoCicilan": getSelectedNoCicilan,
        });
        responseHandler(response, () => {
            const data = response.data.data;
            setNoPembayaran(data.noPembayaran);
            setTotalPembayaranCicilan(data.totalPembayaranCicilan);
            setTotalPembayaranDenda(data.totalPembayaranDenda);
            setBiayaAdminTutup(data.biayaAdminTutup);
            setDiskon(data.diskon);
            setTotalTagihan(data.totalTagihan);
            setPembulatan(data.pembulatan);
            setTotalTagihanSetelahPembulatan(data.totalTagihanSetelahPembulatan);
            setJumlahInputPembayaran(data.jumlahPembayaranPelanggan);
            setKembalian(data.kembalian);
            setMetodeBayar(data.metodePembayaran);
            setSelectedDaftarCicilan(data.selectedDaftarCicilan);

            setIsEdit(false);
        });
    };

    const onClickSave = async () => {
        const response = await Api.Cicilan.doUpdatePembayaran({
            "noTransaksi": getNoTransaksi,
            "metodeBayar": getMetodeBayar,
            "diskon": getDiskon,
            "jumlahInputPembayaran": getJumlahInputPembayaran,
            "selectedNoCicilan": getSelectedNoCicilan,
        });
        responseHandler(response, () => {
            const data = response.data.data;
            setNoPembayaran(data.noPembayaran);
            setTotalPembayaranCicilan(data.totalPembayaranCicilan);
            setTotalPembayaranDenda(data.totalPembayaranDenda);
            setBiayaAdminTutup(data.biayaAdminTutup);
            setDiskon(data.diskon);
            setTotalTagihan(data.totalTagihan);
            setPembulatan(data.pembulatan);
            setTotalTagihanSetelahPembulatan(data.totalTagihanSetelahPembulatan);
            setJumlahInputPembayaran(data.jumlahPembayaranPelanggan);
            setKembalian(data.kembalian);
            setMetodeBayar(data.metodePembayaran);
            setSelectedDaftarCicilan(data.selectedDaftarCicilan);
            setSelectedNoCicilan([]);

            setIsEdit(false);
            successAlert("Cicilan telah dibayar");
            navigate(-1);
        });
    };
    const getDetail = async () => {
        const response = await Api.Cicilan.doGetDetail(id);
        responseHandler(response, () => {
            const data = response.data.data;
            setPelanggan(data.pelanggan);
            setTanggalTransaksi(data.tanggalTransaksi);
            setNoTransaksi(data.noTransaksi);
            setTotalNilaiPinjaman(data.totalNilaiPinjaman);
            setTenor(data.tenor);
            setTanggalJatuhTempo(data.tanggalJatuhTempo);
            setProdukTransaksi(data.produkTransaksi);
            setNamaProduk(data.namaProduk);
            setKeteranganProduk(data.keteranganProduk);
            setTotalKewajiban(data.totalKewajiban);
            setTotalDenda(data.totalDenda);
            setTotalPembayaran(data.totalPembayaran);
            setSisaKewajiban(data.sisaKewajiban);
            setDetailTransaksi(data.detailTransaksi);
        });
    };

    useEffect(() => {
        if (id === undefined) {
            errorAlertThenCloseWindow("Error", "No Transaction ID detected!");
        } else {
            getDetail();
        }
    }, []);

    const detailCicilanLainnya = [
        ["Total Pembayaran Cicilan",
            true,
            getTotalPembayaranCicilan,
            () => {},
            {}],
        ["Total Pembayaran Denda",
            true,
            getTotalPembayaranDenda,
            () => {},
            {}],
        ["Biaya Admin Tutup",
            true,
            getBiayaAdminTutup,
            () => {},
            {}],
        ["Diskon",
            false,
            getDiskon,
            (e) => {setDiskon(e.target.value); setIsEdit(true);},
            {}],
        ["Total Tagihan",
            true,
            getTotalTagihan,
            () => {},
            {}],
        ["Pembulatan",
            true,
            getPembulatan,
            () => {},
            {}],
        ["Total Tagihan Setelah Pembulatan",
            true,
            getTotalTagihanSetelahPembulatan,
            () => {},
            {}],
        ["Jumlah Pembayaran Pelanggan",
            false,
            getJumlahInputPembayaran,
            (e) => {setJumlahInputPembayaran(e.target.value); setIsEdit(true);},
            {}],
        ["Kembalian",
            true,
            getKembalian,
            () => {},
            {}],
        ["Metode Pembayaran",
            false,
            getMetodeBayar,
            (e) => {setMetodeBayar(e.target.value); console.log(e.target.value); setIsEdit(true);},
            {}],
    ];

    const rowRightAlign = [];
    for (let i of detailCicilanLainnya.keys()) {
        const currentRow = detailCicilanLainnya[i];

        let name = currentRow[0];
        if (!currentRow[1]) {
            name = <b>{currentRow[0]}</b>;
        }
        const properties = {
            className: "col s7",
            disabled: currentRow[1],
            value: currentRow[2],
            onChange: currentRow[3],
            ...currentRow[4]
        };

        rowRightAlign.push(
            <div key={rowRightAlign.length} className="row left-align">
                <div className="input-field col s12">
                    <h6 className="col s4">{name}</h6>
                    {currentRow[0] === "Metode Pembayaran" ? (<>
                        <select
                            id="metodeBayar"
                            className="browser-default"
                            value={getMetodeBayar}
                            onChange={properties.onChange}
                        >
                            <option disabled>Choose your option</option>
                            <option value="CASH">CASH</option>
                            <option value="TRANSFER">TRANSFER</option>
                            <option value="DEBIT">DEBIT</option>
                            <option value="CREDIT">CREDIT</option>
                        </select>
                    </>) : (<input {...properties} />)}
                </div>
            </div>
        );
    }

    let buttonsArray = [
        ["Simpan",
            onClickSave,
            "green",
            getIsEdit,
            "add",
        ],
        ["Hitung",
            onClickHitung,
            "blue",
            false,
            "sync",
        ],
        ["Kembali",
            () => navigate(-1),
            "",
            false,
            "arrow_back",
        ],
    ];
    const materialIconsLeft = [];
    for (let i of buttonsArray.keys()) {
        const currentRow = buttonsArray[i];

        const buttonName = currentRow[0];
        const icon = currentRow[4];

        const buttonProperty = {
            onClick: currentRow[1],
            className: "btn " + currentRow[2],
            disabled: currentRow[3],
        };

        materialIconsLeft.push(
            <div key={materialIconsLeft.length} className="col s3 right">
                <button {...buttonProperty} >
                    <i className="material-icons left">{icon}</i>{buttonName}
                </button>
            </div>
        );
    }
    const alterSelectedNoCicilan = (method, no) => {
        const theArray = getSelectedNoCicilan;
        if (method === "ADD") {
            if (!theArray.includes(no)) {
                theArray.push(no);
            }
        }
        if (method === "DELETE") {
            const locationToDelete = theArray.indexOf(no);
            if (locationToDelete > -1) {
                theArray.splice(locationToDelete, 1);
            }
        }
        setSelectedNoCicilan(theArray);
        setIsEdit(true);
    };
    return (
        <fieldset className="container valign-wrapper">
            <div className="center container">
                <h4>Transaksi Cicilan Tetap - Pembayaran Cicilan</h4>
                <fieldset>
                    <CicilanDetailForm
                        noPembayaran={getNoPembayaran}
                        produkTransaksi={getProdukTransaksi}
                        pelanggan={getPelanggan}
                        namaProduk={getNamaProduk}
                        tanggalTransaksi={getTanggalTransaksi}
                        keteranganProduk={getKeteranganProduk}
                        noTransaksi={getNoTransaksi}
                        totalKewajiban={getTotalKewajiban}
                        totalNilaiPinjaman={getTotalNilaiPinjaman}
                        totalDenda={getTotalDenda}
                        tenor={getTenor}
                        totalPembayaran={getTotalPembayaran}
                        tanggalJatuhTempo={getTanggalJatuhTempo}
                        sisaKewajiban={getSisaKewajiban}
                    />
                </fieldset>
                <fieldset>
                    <CicilanDetailTable
                        detailTransaksi={getDetailTransaksi}
                        alterSelectedNoCicilan={alterSelectedNoCicilan}
                    />
                </fieldset>
                <div className="row">
                    {rowRightAlign}
                    <div className={!getIsEdit ? "col s3" : "col s6"} />
                    {materialIconsLeft}
                </div>
            </div>
        </fieldset>
    );
};