import { Link } from "react-router-dom";
import { TransactionPageList } from "../../App";

export const TransactionTable = (props) => {
    const tbody = props.list.map((transaction) => {
        return (
            <tr key={transaction.id}>
                <td>{transaction.id}</td>
                <td>{transaction.tanggalTransaksi}</td>
                <td>{transaction.custId}</td>
                <td>{transaction.custKtp}</td>
                <td>{transaction.custName}</td>
                <td>{transaction.productId}</td>
                <td>{transaction.tanggalJatuhTempo}</td>
                <td>{transaction.status}</td>
                <td>
                    <Link to={TransactionPageList["Detail Transaksi"]["path"]} state={{ id: transaction.id }}>
                        detail
                    </Link>
                </td>
            </tr>
        )
    })
    return (
        <table className="stripped responsive-table">
            <thead>
                <tr>
                    <th>Nomor Transaksi</th>
                    <th>Tanggal Transaksi</th>
                    <th>Id Pelanggan</th>
                    <th>Nomor KTP</th>
                    <th>Nama Pelanggan</th>
                    <th>Produk</th>
                    <th>Tanggal Jatuh Tempo</th>
                    <th>Status Transaksi</th>
                    <th>action</th>
                </tr>
            </thead>
            <tbody>
                {tbody}
            </tbody>
        </table>
    );
};