export const TransactionForm = (props) => {
    const {
        productId,
        productName,
        statusTransaksi,
        trxDateBegin,
        trxDateEnd,
        noTransaksi,
        customerKtp,
        customerId,
        customerName,

        onChangeProductId,
        onChangeProductName,
        onChangeStatusTransaksi,
        onChangeTrxDateBegin,
        onChangeTrxDateEnd,
        onChangeNoTransaksi,
        onChangeCustomerKtp,
        onChangeCustomerId,
        onChangeCustomerName,
        
        onClickSearch,
        onClickEmpty,
        onClickAdd,
    } = props;

    const inputs = [
        [
            ["Kode Produk",
                "col s6 input-field",
                "kodeProduk",
                productId,
                onChangeProductId,
            ],
            ["Status Transaksi",
                "col s6",
                "statusTransaksi",
                statusTransaksi,
                onChangeStatusTransaksi,
            ],
        ],
        [
            ["Nama Produk",
                "col s6 input-field",
                "namaProduk",
                productName,
                onChangeProductName,
            ],
            ["Nomor KTP",
                "col s6 input-field",
                "customerKtp",
                customerKtp,
                onChangeCustomerKtp,
            ],
        ],
        [
            ["Tanggal Transaksi",
                "col s3 input-field",
                "tanggalTransaksi",
                trxDateBegin,
                onChangeTrxDateBegin,
            ],
            ["Id pelanggan",
                "col s6 input-field",
                "idPelanggan",
                customerId,
                onChangeCustomerId,
            ],
        ],
        [
            ["s/d",
                "col s3 input-field",
                "tanggalTransaksiAfter",
                trxDateEnd,
                onChangeTrxDateEnd,
            ],
            ["Nama Pelanggan",
                "col s6 input-field",
                "namaPelanggan",
                customerName,
                onChangeCustomerName,
            ],
        ],
        [
            ["Nomor Transaksi",
                "col s6 input-field",
                "nomorTransaksi",
                noTransaksi,
                onChangeNoTransaksi,
            ],
        ],
    ];
    const rows = [];
    for (let i of inputs.keys()) {
        const tempArray = [];
        for (let j of inputs[i].keys()) {
            const currentRow = inputs[i][j];

            const key = i.toString() + j.toString();
            const inputName = currentRow[0];
            const className = currentRow[1];
            const inputId = currentRow[2];
            const value = currentRow[3];
            const onChange = currentRow[4];

            let type = "text";
            if (["tanggalTransaksi", "tanggalTransaksiAfter"].includes(inputId)) {
                type = "date";
            }
            if (inputName === "Status Transaksi") {
                tempArray.push(
                    <div key={key} className={className}>
                        <label htmlFor={inputId}>{inputName}</label>
                        <select
                            id={inputId}
                            className="browser-default"
                            value={value}
                            onChange={onChange}
                        >
                            <option disabled>Choose your option</option>
                            <option value="">Semua</option>
                            <option value="AKTIF">Aktif</option>
                            <option value="JATUH TEMPO CICILAN">Jatuh Tempo Cicilan</option>
                            <option value="TERLAMBAT BAYAR">Terlambat Bayar</option>
                            <option value="JATUH TEMPO TRANSAKSI">Jatuh Tempo Transaksi</option>
                            <option value="Lunas">Lunas</option>
                        </select>
                    </div>
                );
            } else {
                tempArray.push(
                    <div key={key} className={className}>
                        <label htmlFor={inputId}>{inputName}</label>
                        <input
                            id={inputId}
                            className="validate"
                            value={value}
                            onChange={onChange}
                            type={type}
                        />
                    </div>
                );
            }
        }
        rows.push(tempArray);
    }

    const buttons = [
        ["Search",
            onClickSearch,
            "",
            "search",
        ],
        ["Kosongkan",
            onClickEmpty,
            "grey",
            "delete",
        ],
        ["Transaksi Baru",
            onClickAdd,
            "green",
            "add",
        ],
    ];
    const col_s3 = []
    col_s3.push(<div key={col_s3.length} className="col s3" />);
    for (let i of buttons.keys()) {
        const currentRow = buttons[i];

        const name = currentRow[0];
        const properties = {
            onClick: currentRow[1],
            className: "btn " + currentRow[2]
        };
        const icon = currentRow[3];

        col_s3.push(
            <div key={col_s3.length} className="col s3">
                <button {...properties} >
                    <i className="material-icons left">{icon}</i>{name}
                </button>
            </div>
        );
    }
    rows.push(col_s3);

    const fieldset = [];
    for (let i of rows.keys()) {
        fieldset.push(
            <div key={i} className="row">
                {rows[i]}
            </div>
        );
    }
    
    return (
        <fieldset>
            <div className="row">
                <h5>Transaksi Cicilan Tetap</h5>
                <div className="divider black" />
            </div>
            {fieldset}
        </fieldset>
    );
};