import { useState } from "react";
import { FormInput } from "../../components/FormInput";
import { LoanTable } from "../../components/LoanTable";
import { replaceNonNumber } from "Api";

export const LoanForm = () => {
    const [getDebt, setDebt] = useState("");
    const [getTenor, setTenor] = useState("");
    const [getInterest, setInterest] = useState("");
    const [getList, setList] = useState(null);

    const onDebtChange = (e) => {
        setDebt(replaceNonNumber(e.target.value));
    };
    const onTenorChange = (e) => {
        setTenor(replaceNonNumber(e.target.value));
    };
    const onInterestChange = (e) => {
        setInterest(replaceNonNumber(e.target.value));
    };

    const debtCalculate = (debt, tenor, interest) => {
        if ([debt, tenor].includes(0)) {
            return null;
        }

        const newList = [];
        for (let i = 1; i < tenor + 1; i++) {

            const key = "loan" + i;
            const pokok = debt/tenor;
            const bunga = (pokok * interest) / 100
            const total = pokok * (1 + interest /100);
            const totalHutang = total * (tenor + 1 - i);
            const sisa = total * (tenor - i);

            const row = {
                key: key,
                tenor: i,
                pokok: pokok,
                bunga: bunga,
                total: total,
                totalHutang: totalHutang,
                sisa: sisa,
            };
            newList.push(row);
        }
        return newList;
    };

    const onSubmit = (e) => {
        const newList = debtCalculate(
            Number(getDebt),
            Number(getTenor),
            Number(getInterest)
        );
        setList(newList);
        e.preventDefault();
    };

    const onDelete = (e) => {
        setDebt("0");
        setTenor("0");
        setInterest("0");
    };

    return (
        <div className="container">
            <form onSubmit={onSubmit}>
                <FormInput
                    label="debt"
                    info="Nilai hutang pokok"
                    type="text"
                    value={getDebt}
                    onChange={onDebtChange}
                />
                <FormInput
                    label="tenor"
                    info="Tenor"
                    type="text"
                    value={getTenor}
                    unit="bulan"
                    onChange={onTenorChange}
                />
                <FormInput
                    label="interest"
                    info="Bunga"
                    type="text"
                    value={getInterest}
                    unit="%"
                    onChange={onInterestChange}
                />
                <button className="waves-effect waves-light btn" type="submit">
                    Hitung
                </button>
                <button className="waves-effect waves-light btn" type="button" onClick={onDelete}>
                    Hapus
                </button>
            </form>
            {getList && getList[0] && getList[0].pokok ? <LoanTable list = {getList} /> : null}
        </div>
    );
};