import { toLocaleString_US } from "../../Api/index";

export const DataTagihanTable = (props) => {
    const tbody = props.list.map((tagihan) => {
        return (
            <tr key={tagihan.id.idTransaksi + tagihan.id.cicilanKe}>
                <td>{tagihan.id.idTransaksi}</td>
                <td>{tagihan.id.cicilanKe}</td>
                <td>{toLocaleString_US(tagihan.pokok)}</td>
                <td>{toLocaleString_US(tagihan.pokok*(tagihan.bunga/100))}</td>
                <td>{toLocaleString_US(tagihan.denda)}</td>
                <td>{toLocaleString_US(tagihan.denda + (tagihan.pokok*(tagihan.bunga/100)) +tagihan.pokok)}</td>
                <td>{tagihan.status}</td>
                <td>{tagihan.tanggalAktif}</td>
                <td>{tagihan.tanggalBayar}</td>
            </tr>
        )
    })
    return (
        <table className="stripped responsive-table">
            <thead>
                <tr>
                    <th>No Transaksi</th>
                    <th>Cicilan Ke</th>
                    <th>Pokok</th>
                    <th>B. Jasa Penyimpanan</th>
                    <th>Denda Keterlambatan</th>
                    <th>Total Tagihan</th>
                    <th>Status Cicilan</th>
                    <th>Tanggal Aktif Cicilan</th>
                    <th>Tanggal Bayar</th>
                </tr>
            </thead>
            <tbody>
                {tbody}
            </tbody>
        </table>
    );
};