import axios from "axios";
import { useEffect, useState } from "react";
import { CardProduct } from "../../components/CardProduct";
import { useDispatch, useSelector } from "react-redux";
import { selectCartItems } from "../../redux/CartSlice";
import { addProduct, removeProduct } from "../../redux/CartSlice";
import { CircularPreLoader } from "../../components/CircularPreLoader";
import Swal from "sweetalert2";

const BASE_URL = "https://fakestoreapi.com";
const ADD = "add";
const REMOVE = "remove";

export const MarketPlace = () => {
    const [getList, setList] = useState([]);
    const [getIsLoading, setIsLoading] = useState(false);
    const [getIsError, setIsError] = useState(false);

    const cart = useSelector(selectCartItems);
    const dispatch = useDispatch();

    const handleCart = (product, addOrRemove) => {
        const item = cart.find(it => it.product.id === product.id);
        const prevQuantity = item ? item.quantity : 0;
        let quantityNumber, handleProduct;
        if (addOrRemove === ADD) {
            quantityNumber = prevQuantity + 1;
            handleProduct = addProduct({ product, quantity: quantityNumber});
        }
        else if (addOrRemove === REMOVE) {
            quantityNumber = prevQuantity - 1;
            handleProduct = removeProduct({ product, quantity: quantityNumber});
        }
        const newList = getList.map(element =>
            element.product.id === product.id ? { ...element, quantity: quantityNumber} : element
        );
        return () => {
            dispatch(handleProduct);
            setList(newList);
        };
    };

    useEffect(() => {
        setIsLoading(true);
        axios.get(BASE_URL + "/products")
            .then((response) => {
                const list = response.data
                    ? response.data.map((product) => {
                        const item = cart.find(it => it.product.id === product.id);
                        const prevQuantity = item ? item.quantity : 0;
                        return { product : { ...product }, quantity: prevQuantity }
                    })
                : [];
                setList(list);
                setIsLoading(false);
                setIsError(false);
            })
            .catch((error) => {
                console.log(error);
                setIsLoading(false);
                setIsError(true);
            });
    }, []);

    if (getIsError) {
        Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "No Network Connection!",
        });
    }

    return (
        <div className="center-align">
            <h1>Marketplace</h1>
            {getIsLoading ? <CircularPreLoader /> : null}
            {getList && getList.length > 0 ? (
                <ul className="collection">
                    {getList.map((element) => {
                        return (
                        <CardProduct
                            key={element.product.id}
                            product={element.product}
                            quantity={element.quantity}
                            handleAddToCart={() =>
                                dispatch(handleCart(element.product, ADD))
                            }
                            handleRemoveToCart={() =>
                                dispatch(handleCart(element.product, REMOVE))
                            }
                        />
                        )
                    })}
                </ul>
            ) : null }
        </div>
    );
};