import { useEffect, useState } from "react";
import { Api, replaceNonNumber, responseHandler, toLocaleString_US } from "../../Api/index";
import { InformasiTransaksiForm } from "./InformasiTransaksiForm";
import { DaftarBarangGadaiTable } from "./DaftarBarangGadaiTable";
import { DataKontrakForm } from "./DataKontrakForm";
import { errorAlert, successAlert } from "../../alert/SweetAlert";
import { CariDataPelangganForm } from "./CariDataPelangganForm";
import { useNavigate } from "../../../node_modules/react-router-dom/dist/index";

export const TransactionAdd = () => {

    const localStorageCustomerId = "customerString.id";
    const localStorageCustomerName = "customerString.name";
    const localStorageCustomerKtp = "customerString.ktp";
    const localStorageCustomerHp = "customerString.hp";

    const [getIdPelanggan, setIdPelanggan] = useState(localStorage.getItem(localStorageCustomerId) || "");
    const [getNamaPelanggan, setNamaPelanggan] = useState(localStorage.getItem(localStorageCustomerName) || "");
    const [getNoKtp, setNoKtp] = useState(localStorage.getItem(localStorageCustomerKtp) || "");
    const [getNoHp, setNoHp] = useState(localStorage.getItem(localStorageCustomerHp) || "");
    const [getHasilPencarianPelanggan, setHasilPencarianPelanggan] = useState([]);

    const [getNamaBarang, setNamaBarang] = useState("");
    const [getKondisiBarang, setKondisiBarang] = useState("");
    const [getJumlahBarang, setJumlahBarang] = useState("");
    const [getHargaPerSatuanBarang, setHargaPerSatuanBarang] = useState("");


    const [getPelanggan, setPelanggan] = useState("");
    const [getTanggalTransaksi, setTanggalTransaksi] = useState("");
    const [getNoTransaksi, setNoTransaksi] = useState("");
    const [getListProductId, setListProductId] = useState([]);
    const [getProductId, setProductId] = useState("");
    const [getProductName, setProductName] = useState("");
    const [getProductDesc, setProductDesc] = useState("");
    const [getDaftarBarangGadai, setDaftarBarangGadai] = useState([]);

    const [getTotalNilaiTaksiran, setTotalNilaiTaksiran] = useState(0);
    const [getLtv, setLtv] = useState(0);
    const [getMaksimalNilaiPinjaman, setMaksimalNilaiPinjaman] = useState(0);
    const [getNilaiPencarianPelanggan, setNilaiPencarianPelanggan] = useState(0);
    const [getBiayaAdminBuka, setBiayaAdminBuka] = useState(0);
    const [getDiskonAdminBuka, setDiskonAdminBuka] = useState(0);
    const [getBiayaAdminBukaAkhir, setBiayaAdminBukaAkhir] = useState(0);
    const [getTotalNilaiPinjaman, setTotalNilaiPinjaman] = useState(0);
    const [getTanggalJatuhTempo, setTanggalJatuhTempo] = useState(0);
    const [getBiayaJasaPenyimpanan, setBiayaJasaPenyimpanan] = useState(0);
    const [getBiayaJasaPenyimpananPeriode, setBiayaJasaPenyimpananPeriode] = useState(0);
    const [getBiayaJasaPenyimpananPerPeriode, setBiayaJasaPenyimpananPerPeriode] = useState(0);
    const [getTotalBiayaJasaPenyimpanan, setTotalBiayaJasaPenyimpanan] = useState(0);
    const [getBiayaAdminTutup, setBiayaAdminTutup] = useState(0);
    const [getTotalPengembalian, setTotalPengembalian] = useState(0);
    
    const [getSaveDisabled, setSaveDisabled] = useState(true);

    const navigate = useNavigate();

    useEffect(() => {
        getListProduk();
    }, []);

    const getDetailProduk = async (productId)=> {
        const response = await Api.Transaction.doGetDetailProduk(productId);
        responseHandler(response, () => {
            setProductId(productId);
            setProductName(response.data.data.name);
            setProductDesc(response.data.data.desc);
            setSaveDisabled(true);
        });
    }

    const getListProduk = async () => {
        const response = await Api.Transaction.doGetListProduk();
        responseHandler(response, () => {
            setListProductId(response.data.data);
            getDetailProduk(response.data.data[0]);
        });
    };

    const onClickSave = async () => {
        let totalNilaiTaksiran = 0;
        for (let i of getDaftarBarangGadai.keys()) {
            const selectedBarang = getDaftarBarangGadai[i];
            totalNilaiTaksiran += (selectedBarang.jlh * selectedBarang.hargaPerSatuan);
        }
        const transaksiModel = {
            productId: getProductId,
            totalNilaiTaksiran,
            nilaiPencarianPelanggan: getNilaiPencarianPelanggan,
            diskonAdmBuka: getDiskonAdminBuka,
            custId: getPelanggan,
            daftarBarangGadai: getDaftarBarangGadai,
        };
        console.log(JSON.stringify(transaksiModel));
        const response = await Api.Transaction.doSave(transaksiModel);
        responseHandler(response, () => {
            const result = response.data.data;
            setTanggalTransaksi(result.trxDate);
            setNoTransaksi(result.noTransaksi);
            successAlert("Berhasil menyimpan transaksi!");
        });
    };

    const onClickHitung = async () => {
        let totalNilaiTaksiran = 0;
        for (let i of getDaftarBarangGadai.keys()) {
            const selectedBarang = getDaftarBarangGadai[i];
            totalNilaiTaksiran += (selectedBarang.jlh * selectedBarang.hargaPerSatuan);
        }
        const transaksiModel = {
            productId: getProductId,
            totalNilaiTaksiran,
            nilaiPencarianPelanggan: getNilaiPencarianPelanggan,
            diskonAdmBuka: getDiskonAdminBuka,
            custId: getPelanggan,
            daftarBarangGadai: getDaftarBarangGadai,
        };
        console.log(JSON.stringify(transaksiModel));
        const response = await Api.Transaction.doHitung(transaksiModel);
        responseHandler(response, () => {
            const result = response.data.data;
            setTotalNilaiTaksiran(parseFloat(result.totalNilaiTaksiran));
            setLtv(parseFloat(result.ltv));
            setMaksimalNilaiPinjaman(parseFloat(result.maksimalNilaiPinjaman));
            setNilaiPencarianPelanggan(parseFloat(result.nilaiPencarianPelanggan));
            setBiayaAdminBuka(parseFloat(result.biayaAdminBuka));
            setDiskonAdminBuka(parseFloat(result.diskonAdminBuka));
            setBiayaAdminBukaAkhir(parseFloat(result.biayaAdminBukaAkhir));
            setTotalNilaiPinjaman(parseFloat(result.totalNilaiPinjaman));
            setTanggalTransaksi(result.tanggalTransaksi);
            setTanggalJatuhTempo(result.tanggalJatuhTempo);
            setBiayaJasaPenyimpanan(parseFloat(result.biayaJasaPenyimpanan));
            setBiayaJasaPenyimpananPeriode(parseFloat(result.biayaJasaPenyimpananPeriode));
            setBiayaJasaPenyimpananPerPeriode(parseFloat(result.biayaJasaPenyimpananPerPeriode));
            setTotalBiayaJasaPenyimpanan(parseFloat(result.totalBiayaJasaPenyimpanan));
            setBiayaAdminTutup(parseFloat(result.biayaAdminTutup));
            setTotalPengembalian(parseFloat(result.totalPengembalian));
            setSaveDisabled(false);
        });
    };

    const onClickSearchPelanggan = async () => {
        
        localStorage.setItem(localStorageCustomerId, getIdPelanggan);
        localStorage.setItem(localStorageCustomerName, getNamaPelanggan);
        localStorage.setItem(localStorageCustomerKtp, getNoKtp);
        localStorage.setItem(localStorageCustomerHp, getNoHp);

        const search = {
            id: getIdPelanggan,
            name: getNamaPelanggan,
            ktp: getNoKtp,
            hp: getNoHp,
        };

        const response = await Api.Transaction.doSearchPelanggan(search);
        responseHandler(response, () => {
            const customers = response.data.data;
            setHasilPencarianPelanggan(customers);
        });

    };

    const onClickKosongkanSearchPelanggan = () => {
        localStorage.setItem(localStorageCustomerId, "");
        localStorage.setItem(localStorageCustomerName, "");
        localStorage.setItem(localStorageCustomerKtp, "");
        localStorage.setItem(localStorageCustomerHp, "");

        setIdPelanggan("");
        setNamaPelanggan("");
        setNoKtp("");
        setNoHp("");
    }

    const inputs = [
        ["Nama Barang",
            "namaBarang",
            getNamaBarang,
            (e) => setNamaBarang(e.target.value),
        ],
        ["Kondisi",
            "kondisiBarang",
            getKondisiBarang,
            (e) => setKondisiBarang(e.target.value),
        ],
        ["Jumlah",
            "jumlahBarang",
            getJumlahBarang,
            (e) => setJumlahBarang(replaceNonNumber(e.target.value)),
        ],
        ["Harga Per Satuan",
            "hargaPerSatuanBarang",
            toLocaleString_US(getHargaPerSatuanBarang),
            (e) => setHargaPerSatuanBarang(replaceNonNumber(e.target.value)),
        ],
    ];
    
    const rows = [];
    for (let i of inputs.keys()) {
        const currentRow = inputs[i];

        const inputName = currentRow[0];
        const inputId = currentRow[1];
        const value = currentRow[2];
        const onChange = currentRow[3];
        const className = "col s6 materialize-textarea";

        const properties = {
            id: inputId,
            className: className,
            value: value,
            onChange: onChange,
        };
        rows.push(
            <div key={rows.length} className={className}>
                <h6 className="col s6 pull-s0">{inputName}</h6>
                <input {...properties}/>
            </div>
        );
    }
    const deleteBarang = (no) => {
        const barangSebelum = getDaftarBarangGadai.slice(0, no - 1);
        const barangSesudah = getDaftarBarangGadai.slice(no, getDaftarBarangGadai.length)
        for (const i of barangSesudah.keys()) {
            barangSesudah[i].no = barangSesudah[i].no - 1;
        }
        const outputArray = [...barangSebelum, ...barangSesudah];
        setDaftarBarangGadai(outputArray);
        setSaveDisabled(true);
    }

    const selectPelanggan = (no) => {
        if (getPelanggan !== no) {
            setPelanggan(no);
            setSaveDisabled(true);
        }
    }
    rows.push(
        <button key={rows.length}
        onClick={() => {
            const checkBarangIf = [getNamaBarang, getKondisiBarang, getJumlahBarang, getHargaPerSatuanBarang];
            const empty = [null, "", undefined];
            if (checkBarangIf.some(kolom => empty.includes(kolom))) {
                errorAlert("Semua kolom barang harus terisi");
            } else if (Number(getJumlahBarang) > 999) {
                errorAlert("Jumlah barang tidak bisa lebih dari 999");
            } else {
                let no = getDaftarBarangGadai.length + 1;
                const barang = {
                    "no": no,
                    "namaBarang": getNamaBarang,
                    "kondisi": getKondisiBarang,
                    "jlh": Number(getJumlahBarang),
                    "hargaPerSatuan": Number(getHargaPerSatuanBarang),
                };
                setDaftarBarangGadai(current => [...current, barang]);
                setSaveDisabled(true);
            }
        }}
        className="btn"
        >
            <i className="material-icons left">add</i>
            Tambahkan
        </button>
    );

    const buttons = [
        ["Kembali",
            () => navigate(-1),
            "",
            false,
            "arrow_back",],
        ["Hitung",
            () => onClickHitung(),
            "blue",
            false,
            "sync",],
        ["Simpan",
            () => onClickSave(),
            "green",
            getSaveDisabled,
            "save",],
    ];
    const buttonRow = [];
    for (const i of buttons.keys()) {
        const currentRow = buttons[i];

        const name = currentRow[0];
        const properties = {
            onClick: currentRow[1],
            className: "btn " + currentRow[2],
            disabled: currentRow[3],
        };
        const icon = currentRow[4];

        buttonRow.push(
            <div key={buttonRow.length} className="col">
                <button {...properties}>
                    <i className="material-icons left">{icon}</i>
                    {name}
                </button>
            </div>
        );
    }

    return (
        <fieldset className="container valign-wrapper">
            <div>
                <h4>Transaksi Cicilan Tetap - Detail</h4>
                <fieldset>
                    <CariDataPelangganForm 
                        idPelanggan={getIdPelanggan}
                        hasilPencarian={getHasilPencarianPelanggan}
                        namaPelanggan={getNamaPelanggan}
                        noKtp={getNoKtp}
                        noHp={getNoHp}

                        onChangeIdPelanggan={(e) => setIdPelanggan(e.target.value)}
                        onChangeHasilPencarian={(e) => setHasilPencarianPelanggan(e.target.value)}
                        onChangeNamaPelanggan={(e) => setNamaPelanggan(e.target.value)}
                        onChangeNoKtp={(e) => setNoKtp(replaceNonNumber(e.target.value))}
                        onChangeNoHp={(e) => setNoHp(replaceNonNumber(e.target.value))}
                
                        onClickSelect={selectPelanggan}
                        onClickSearch={onClickSearchPelanggan}
                        onClickEmpty={onClickKosongkanSearchPelanggan}
                    />
                </fieldset>
                <fieldset>
                    <InformasiTransaksiForm
                        pelanggan={getPelanggan}
                        tanggalTransaksi={getTanggalTransaksi}
                        noTransaksi={getNoTransaksi}
                        listProductId={getListProductId}
                        productName={getProductName}
                        productDesc={getProductDesc}

                        onChangeSelection={getDetailProduk}
                    />
                </fieldset>
                <fieldset>
                    {rows}
                </fieldset>
                {getDaftarBarangGadai.length === 0 ? null : (
                <fieldset>
                    <DaftarBarangGadaiTable list={getDaftarBarangGadai} onClickDelete={deleteBarang} />
                </fieldset>
                )}
                <fieldset>
                <DataKontrakForm 
                    totalNilaiTaksiran={getTotalNilaiTaksiran}
                    ltv={getLtv}
                    maksimalNilaiPinjaman={getMaksimalNilaiPinjaman}
                    nilaiPencarianPelanggan={getNilaiPencarianPelanggan}
                    biayaAdminBuka={getBiayaAdminBuka}
                    diskonAdminBuka={getDiskonAdminBuka}
                    biayaAdminBukaAkhir={getBiayaAdminBukaAkhir}
                    totalNilaiPinjaman={getTotalNilaiPinjaman}
                    tanggalTransaksi={getTanggalTransaksi}
                    tanggalJatuhTempo={getTanggalJatuhTempo}
                    biayaJasaPenyimpanan={getBiayaJasaPenyimpanan}
                    biayaJasaPenyimpananPeriode={getBiayaJasaPenyimpananPeriode}
                    biayaJasaPenyimpananPerPeriode={getBiayaJasaPenyimpananPerPeriode}
                    totalBiayaJasaPenyimpanan={getTotalBiayaJasaPenyimpanan}
                    biayaAdminTutup={getBiayaAdminTutup}
                    totalPengembalian={getTotalPengembalian}
                    
                    onChangeDiskonAdminBuka={(e) => {
                        setDiskonAdminBuka(replaceNonNumber(e.target.value));
                        setSaveDisabled(true);
                    }}
                    onChangeNilaiPencarianPelanggan={(e) => {
                        setNilaiPencarianPelanggan(replaceNonNumber(e.target.value));
                        setSaveDisabled(true);
                    }}

                    nilaiPencarianPelanggan_diskonAdminBuka_editable={true}
                />
                </fieldset>
                <div className="row">
                    {buttonRow}
                </div>
            </div>
        </fieldset>
    );
};