export const UserForm = (props) => {
    const {
        id,
        name,
        hp,
        desc,
        recStatus,

        onChangeId,
        onChangeName,
        onChangeHp,
        onChangeDesc,
        onChangeRecStatus,
        
        onClickSearch,
        onClickEmpty,
        onClickAdd,
    } = props;

    const inputs = [
        [
            ["User Id",
                "col s6 input-field",
                "userId",
                id,
                onChangeId,
            ],
            ["Status",
                "col s6",
                "status",
                recStatus,
                onChangeRecStatus,
            ],
        ],
        [
            ["User Name",
                "col s6 input-field",
                "username",
                name,
                onChangeName,
            ],
            ["Nomor Hp",
                "col s6 input-field",
                "nomorHp",
                hp,
                onChangeHp,
            ],
        ],
        [
            ["Keterangan",
                "col s12 input-field",
                "keterangan",
                desc,
                onChangeDesc,
            ],
        ],
    ];
    
    const rows = [];
    for (let i of inputs.keys()) {
        const tempArray = [];
        for (let j of inputs[i].keys()) {
            const currentRow = inputs[i][j];
            const key = i.toString() + j.toString();

            const inputName = currentRow[0];
            const className = currentRow[1];
            const inputId = currentRow[2];
            const value = currentRow[3];
            const onChange = currentRow[4];

            if (inputName === "Status") {
                tempArray.push(
                    <div key={key} className={className}>
                        <label htmlFor={inputId}>{inputName}</label>
                        <select
                            id={inputId}
                            className="browser-default"
                            value={value}
                            onChange={onChange}
                        >
                            <option disabled>Choose your option</option>
                            <option value="All">All</option>
                            <option value="A">Active</option>
                            <option value="N">Non-Active</option>
                        </select>
                    </div>
                );
            } else {
                tempArray.push(
                    <div key={key} className={className}>
                        <label htmlFor={inputId}>{inputName}</label>
                        <input
                            id={inputId}
                            className="validate"
                            value={value}
                            onChange={onChange}
                            type="text"
                        />
                    </div>
                );
            }
        }
        rows.push(tempArray);
    }

    const buttons = [
        ["Search",
            onClickSearch,
            "",
            "search",
        ],
        ["Kosongkan",
            onClickEmpty,
            "grey",
            "delete",
        ],
        ["User Baru",
            onClickAdd,
            "green",
            "add",
        ],
    ];
    const col_s3 = []
    col_s3.push(<div key={col_s3.length} className="col s3" />);
    for (let i of buttons.keys()) {
        const currentRow = buttons[i];

        const name = currentRow[0];
        const properties = {
            onClick: currentRow[1],
            className: "btn " + currentRow[2],
        };
        const icon = currentRow[3];

        col_s3.push(
            <div key={col_s3.length} className="col s3">
                <button {...properties} >
                    <i className="material-icons left">{icon}</i>{name}
                </button>
            </div>
        );
    }
    rows.push(col_s3);

    const fieldset = [];
    for (let i of rows.keys()) {
        fieldset.push(
            <div key={i} className="row">
                {rows[i]}
            </div>
        );
    }
    
    return (
        <fieldset>
            <div className="row">
                <h5>Cari data User</h5>
                <div className="divider black" />
            </div>
            {fieldset}
        </fieldset>
    );
};