import { useEffect, useState } from "react";
import { TransactionForm } from "./TransactionForm";
import { useNavigate, useSearchParams } from "react-router-dom";
import { Api, currentPage, responseHandler } from "../../Api";
import { CircularPreLoader } from "../../components/CircularPreLoader";
import { TransactionTable } from "./TransactionTable";
import { Pagination } from "../../components/Pagination";
import { TransactionPageList } from "../../App";

export const Transaction = () => {

    const localStorageProductId = "transaction.productId";
    const localStorageProductName = "transaction.productName";
    const localStorageTrxDateBegin = "transaction.trxDateBegin";
    const localStorageTrxDateEnd = "transaction.trxDateEnd";
    const localStorageNoTransaksi = "transaction.noTransaksi";
    const localStorageStatusTransaksi = "transaction.statusTransaksi";
    const localStorageCustomerKtp = "transaction.customerKtp";
    const localStorageCustomerId = "transaction.customerId";
    const localStorageCustomerName = "transaction.customerName";
    const localStorageRecStatus = "transaction.recStatus";

    const [getProductId, setProductId] = useState(localStorage.getItem(localStorageProductId) || "");
    const [getProductName, setProductName] = useState(localStorage.getItem(localStorageProductName) || "");
    const [getTrxDateBegin, setTrxDateBegin] = useState(localStorage.getItem(localStorageTrxDateBegin) || "");
    const [getTrxDateEnd, setTrxDateEnd] = useState(localStorage.getItem(localStorageTrxDateEnd) || "");
    const [getNoTransaksi, setNoTransaksi] = useState(localStorage.getItem(localStorageNoTransaksi) || "");
    const [getStatusTransaksi, setStatusTransaksi] = useState(localStorage.getItem(localStorageStatusTransaksi) || "");
    const [getCustomerKtp, setCustomerKtp] = useState(localStorage.getItem(localStorageCustomerKtp) || "");
    const [getCustomerId, setCustomerId] = useState(localStorage.getItem(localStorageCustomerId) || "");
    const [getCustomerName, setCustomerName] = useState(localStorage.getItem(localStorageCustomerName) || "");
    const [getRecStatus, setRecStatus] = useState(localStorage.getItem(localStorageRecStatus) || "");

    const [getList, setList] = useState([]);
    const [getIsLoading, setIsLoading] = useState(false);
    const [getMaxItems, setMaxItems] = useState(0);
    const [getTotalPages, setTotalPages] = useState(0);
    const [getSearchParameter, setSearchParameter] = useSearchParams();

    const navigate = useNavigate();

    const onClickSearch = async (page) => {
        setIsLoading(true);
        
        const updatedSearchParams = new URLSearchParams(getSearchParameter.toString());
        updatedSearchParams.set('page', page);
        setSearchParameter(updatedSearchParams.toString());

        localStorage.setItem(localStorageProductId, getProductId);
        localStorage.setItem(localStorageProductName, getProductName);
        localStorage.setItem(localStorageTrxDateBegin, getTrxDateBegin);
        localStorage.setItem(localStorageTrxDateEnd, getTrxDateEnd);
        localStorage.setItem(localStorageNoTransaksi, getNoTransaksi);
        localStorage.setItem(localStorageStatusTransaksi, getStatusTransaksi);
        localStorage.setItem(localStorageCustomerKtp, getCustomerKtp);
        localStorage.setItem(localStorageCustomerId, getCustomerId);
        localStorage.setItem(localStorageCustomerName, getCustomerName);
        localStorage.setItem(localStorageRecStatus, getRecStatus);

        const search = {
            productId: getProductId,
            productName: getProductName,
            trxDateBegin: getTrxDateBegin,
            trxDateEnd: getTrxDateEnd,
            noTransaksi: getNoTransaksi,
            statusTransaksi: getStatusTransaksi,
            customerKtp: getCustomerKtp,
            customerId: getCustomerId,
            customerName: getCustomerName,
            recStatus: getRecStatus,
        };

        const response = await Api.Transaction.doSearch(search, page);
        responseHandler(response, () => {
            setList(response.data.data.results);
            setMaxItems(response.data.data.maxItems);
            setTotalPages(response.data.data.totalPages);
        });

        setIsLoading(false); 
    };

    useEffect(() => {
        onClickSearch(currentPage(getSearchParameter));
    }, []);

    return (
        <div className="container">
            <TransactionForm
                productId={getProductId}
                productName={getProductName}
                trxDateBegin={getTrxDateBegin}
                trxDateEnd={getTrxDateEnd}
                noTransaksi={getNoTransaksi}
                statusTransaksi={getStatusTransaksi}
                customerKtp={getCustomerKtp}
                customerId={getCustomerId}
                customerName={getCustomerName}

                onChangeProductId={(e) => setProductId(e.target.value)}
                onChangeProductName={(e) => setProductName(e.target.value)}
                onChangeTrxDateBegin={(e) => setTrxDateBegin(e.target.value)}
                onChangeTrxDateEnd={(e) => setTrxDateEnd(e.target.value)}
                onChangeNoTransaksi={(e) => setNoTransaksi(e.target.value)}
                onChangeStatusTransaksi={(e) => setStatusTransaksi(e.target.value)}
                onChangeCustomerKtp={(e) => setCustomerKtp(e.target.value)}
                onChangeCustomerId={(e) => setCustomerId(e.target.value)}
                onChangeCustomerName={(e) => setCustomerName(e.target.value)}

                onClickSearch={() => {
                    onClickSearch(1);
                }}
                onClickEmpty={() => {
                    setProductId("");
                    setProductName("");
                    setTrxDateBegin("");
                    setTrxDateEnd("");
                    setNoTransaksi("");
                    setStatusTransaksi("");
                    setCustomerKtp("");
                    setCustomerId("");
                    setCustomerName("");
                    setRecStatus("");
                    localStorage.setItem(localStorageProductId, "");
                    localStorage.setItem(localStorageProductName, "");
                    localStorage.setItem(localStorageTrxDateBegin, "");
                    localStorage.setItem(localStorageTrxDateEnd, "");
                    localStorage.setItem(localStorageNoTransaksi, "");
                    localStorage.setItem(localStorageStatusTransaksi, "");
                    localStorage.setItem(localStorageCustomerKtp, "");
                    localStorage.setItem(localStorageCustomerId, "");
                    localStorage.setItem(localStorageCustomerName, "");
                    localStorage.setItem(localStorageRecStatus, "");
                    navigate("?page=1");
                }}
                onClickAdd={() => navigate(TransactionPageList["Transaksi Baru"]["path"])}
            />
            {getIsLoading ? (
                <div className="valign-wrapper">
                    <CircularPreLoader />
                </div>
            ) : (
                <fieldset>
                    {getList && getList.length > 0 ? <TransactionTable list={getList.slice(0, getMaxItems)} /> : null}
                </fieldset>
            )}
            <Pagination onClick={onClickSearch} page={currentPage(getSearchParameter)} maxItems={getMaxItems} totalPages={getTotalPages}  itemNumbers={getList ? getList.length : 0}/>
        </div>
    );
};