import { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import { errorAlertThenCloseWindow, successAlert } from "../../alert/SweetAlert";
import { Api, formatDecimal, replaceNonNumber, responseHandler, toLocaleString_US } from "../../Api";

export const ProductDetail = () => {
    const location = useLocation();
    const locationState = (location.state || {});
    const id = locationState.id;

    const [getType, setType] = useState("");
    const [getName, setName] = useState("");
    const [getDesc, setDesc] = useState("");
    const [getLtv, setLtv] = useState(0);
    const [getJangkaWaktu, setJangkaWaktu] = useState(0);
    const [getBiayaAdminBuka, setBiayaAdminBuka] = useState(0);
    const [getBiayaAdminBukaType, setBiayaAdminBukaType] = useState("");
    const [getBiayaAdminTutup, setBiayaAdminTutup] = useState(0);
    const [getBiayaAdminTutupType, setBiayaAdminTutupType] = useState("");
    const [getBiayaJasaPenyimpanan, setBiayaJasaPenyimpanan] = useState(0);
    const [getBiayaJasaPenyimpananPeriode, setBiayaJasaPenyimpananPeriode] = useState(0);
    const [getBiayaDenda, setBiayaDenda] = useState(0);
    const [getBiayaDendaPeriode, setBiayaDendaPeriode] = useState(0);
    const [getCreatedDate, setCreatedDate] = useState();
    const [getCreatedBy, setCreatedBy] = useState();
    const [getUpdatedDate, setUpdatedDate] = useState();
    const [getUpdatedBy, setUpdatedBy] = useState();
    const [getRecStatus, setRecStatus] = useState();
    const [getIsEdit, setIsEdit] = useState(false);
    const [getTitle, setTitle] = useState("Pengaturan Product - Detail");

    const navigate = useNavigate();

    const onClickEdit = () => {
        setTitle("Pengaturan Product - Ubah");
        setIsEdit(true)
    };

    const onClickDelete = () => {
        Swal.fire({
            title: "Do you want to delete this product?",
            showDenyButton: true,
            confirmButtonText: "Yes",
            denyButtonText: "No",
        }).then(async (result) => {
            if (result.isConfirmed) {
                const response = await Api.Product.doDelete({
                    id: id,  
                    actorId: 0,
                });
                responseHandler(response, () => {
                    successAlert("Product has been deleted");
                    navigate(-1);
                });
            }
            else if (result.isDenied) {
                Swal.fire("Product is not removed", "", "info");
            };
        });
    };
    const onClickSave = async () => {
        Swal.fire({
            title: "Do you want to save the changes?",
            showDenyButton: true,
            confirmButtonText: "Yes",
            denyButtonText: "No",
        }).then(async (result) => {
            if (result.isConfirmed) {
                const response = await Api.Product.doUpdate({
                    id: id,
                    type: getType,
                    name: getName,
                    desc: getDesc,
                    ltv: getLtv,
                    jangkaWaktu: getJangkaWaktu,
                    biayaAdminBuka: getBiayaAdminBuka,
                    biayaAdminBukaType: getBiayaAdminBukaType,
                    biayaAdminTutup: getBiayaAdminTutup,
                    biayaAdminTutupType: getBiayaAdminTutupType,
                    biayaJasaPenyimpanan: getBiayaJasaPenyimpanan,
                    biayaJasaPenyimpananPeriode: getBiayaJasaPenyimpananPeriode,
                    biayaDenda: getBiayaDenda,
                    biayaDendaPeriode: getBiayaDendaPeriode,
                });
                responseHandler(response, () => {
                    successAlert("Product has been updated");
                });
            }
            else if (result.isDenied) {
                Swal.fire("Changes are not saved", "", "info");
            };
        });
    };
    const getDetail = async () => {
        const response = await Api.Product.doGetDetail(location.state.id);
        responseHandler(response, () => {
            const details = response.data.data;
            setType(details.type);
            setName(details.name);
            setDesc(details.desc);
            setLtv(details.ltv);
            setJangkaWaktu(details.jangkaWaktu);
            setBiayaAdminBuka(details.biayaAdminBuka);
            setBiayaAdminBukaType(details.biayaAdminBukaType);
            setBiayaAdminTutup(details.biayaAdminTutup);
            setBiayaAdminTutupType(details.biayaAdminTutupType);
            setBiayaJasaPenyimpanan(details.biayaJasaPenyimpanan);
            setBiayaJasaPenyimpananPeriode(details.biayaJasaPenyimpananPeriode);
            setBiayaDenda(details.biayaDenda);
            setBiayaDendaPeriode(details.biayaDendaPeriode);
            setUpdatedBy(details.updaterId);
            setRecStatus(details.recStatus);
            setCreatedDate(details.createdDate);
            setCreatedBy(details.creatorId);
            setUpdatedDate(details.updatedDate);
            setUpdatedBy(details.updaterId);
            setRecStatus(details.recStatus);
        });
    };

    useEffect(() => {
        if (id === undefined) {
            errorAlertThenCloseWindow("Error", "No Product ID detected!");
        } else {
            getDetail();
        }
    }, []);

    const productSettings = [
        [
            ["Produk Id",
                true,
                id,
                () => {},
                {}]
        ],
        [
            ["Tipe Produk",
                true,
                getType,
                (e) => setType(e.target.value),
                {}]
        ],
        [
            ["Nama Produk",
                !getIsEdit,
                getName,
                (e) => setName(e.target.value),
                {}]
        ],
        [
            ["Keterangan",
                !getIsEdit,
                getDesc,
                (e) => setDesc(e.target.value),
                {}]
        ],
        [
            ["LTV",
                !getIsEdit,
                getLtv,
                (e) => setLtv(formatDecimal(e.target.value)),
                {},
                "%"]
        ],
        [
            ["Jangka Waktu",
                !getIsEdit,
                getJangkaWaktu,
                (e) => setJangkaWaktu(replaceNonNumber(e.target.value)),
                {},
                "hari / bulan"]
        ],
        [
            ["Biaya Admin Buka",
                !getIsEdit,
                getBiayaAdminBukaType,
                (e) => setBiayaAdminBukaType(e.target.value),
                { type: "radio", name: "biayaAdminBuka" }],
            ["",
                !getIsEdit,
                toLocaleString_US(getBiayaAdminBuka),
                (e) => setBiayaAdminBuka(formatDecimal(e.target.value)),
                {},
                getBiayaAdminBukaType === "PERSEN" ? "%" : ""]
        ],
        [
            ["Biaya Admin Tutup",
                !getIsEdit,
                getBiayaAdminTutupType,
                (e) => setBiayaAdminTutupType(e.target.value),
                { type: "radio", name: "biayaAdminTutup" }],
            ["",
                !getIsEdit,
                toLocaleString_US(getBiayaAdminTutup),
                (e) => setBiayaAdminTutup(formatDecimal(e.target.value)),
                {},
                getBiayaAdminTutupType === "PERSEN" ? "%" : ""]
        ],
        [
            ["Biaya Jasa Penyimpanan",
                !getIsEdit,
                toLocaleString_US(getBiayaJasaPenyimpanan),
                (e) => setBiayaJasaPenyimpanan(formatDecimal(e.target.value)),
                {}],
            ["Per",
                !getIsEdit,
                getBiayaJasaPenyimpananPeriode,
                (e) => setBiayaJasaPenyimpananPeriode(replaceNonNumber(e.target.value)),
                {},
                "hari / bulan"]
        ],
        [
            ["Biaya Denda Keterlambatan",
                !getIsEdit,
                toLocaleString_US(getBiayaDenda),
                (e) => setBiayaDenda(formatDecimal(e.target.value)),
                {}],
            ["Per",
                !getIsEdit,
                getBiayaDendaPeriode,
                (e) => setBiayaDendaPeriode(replaceNonNumber(e.target.value)),
                {},
                "hari"]
        ],
        [
            ["Tanggal Buat / Oleh",
                true,
                getCreatedBy ? `${new Date(getCreatedDate).toLocaleDateString("sv-SE")} / ${getCreatedBy}` : "-",
                () => {},
                {}]
        ],
        [
            ["Tanggal Update Terakhir / Oleh",
                true,
                getUpdatedBy ? `${new Date(getUpdatedDate).toLocaleDateString("sv-SE")} / ${getUpdatedBy}` : "-",
                () => {},
                {}]
        ],
    ];
    const rowLeftAlign = [];
    for (let i of productSettings.keys()) {
        const currentRow = productSettings[i];
        let element = [];
        for (let j of currentRow.keys()) {
            const currentPage = currentRow[j];
            const length = 12/currentRow.length
            const properties = {
                className: "col s7 materialize-textarea",
                disabled: currentPage[1],
                value: currentPage[2],
                onChange: currentPage[3],
                ...currentPage[4],
            };
            element.push(["Biaya Admin Tutup", "Biaya Admin Buka"].includes(currentPage[0]) ? (
                <div key={element.length} className={"input-field col s" + length}>
                    <h6 className="col s3 pull-s0">{currentPage[0]}</h6>
                    <form>
                            <label>
                                <input
                                    value="NOMINAL"
                                    checked={currentPage[0] === "Biaya Admin Buka" ?
                                        (getBiayaAdminBukaType === "NOMINAL") :
                                        (getBiayaAdminTutupType === "NOMINAL")
                                    }
                                    onChange={properties.onChange}
                                    disabled={properties.disabled}
                                    {...currentPage[4]}
                                />
                                <span>NOMINAL</span>
                            </label>
                            <label>
                                <input
                                    value="PERSEN"
                                    checked={currentPage[0] === "Biaya Admin Buka" ?
                                        (getBiayaAdminBukaType === "PERSEN") :
                                        (getBiayaAdminTutupType === "PERSEN")
                                    }
                                    onChange={properties.onChange}
                                    disabled={properties.disabled}
                                    {...currentPage[4]}
                                />
                                <span>PERSEN</span>
                            </label>
                        </form>
                </div>) : (
                <div key={element.length} className={"input-field col s" + length}>
                    <h6 className="col s3 pull-s0">{currentPage[0]}</h6>
                    {currentPage[0] === "Keterangan" ? (<textarea {...properties}/>) : (<input {...properties}/>)}
                    {currentPage[5]}
                </div>)
            );
        }
        rowLeftAlign.push(
            <div key={rowLeftAlign.length} className="row left-align">
                {element}
            </div>
        );
    }

    let buttons = [
        ["Kembali",
            () => navigate(-1),
            "",
            false,
            "arrow_back",
        ],
    ];
    if (!getIsEdit) {
        buttons = buttons.concat([
            ["Hapus",
                onClickDelete,
                "red",
                getRecStatus === "N",
                "delete",
            ],
            ["Ubah",
                onClickEdit,
                "green",
                getRecStatus === "N",
                "edit",
            ],
        ]);
    }
    else {
        buttons = buttons.concat([
            ["Simpan",
                onClickSave,
                "green",
                false,
                "save",
            ],
        ]);
    }
    const materialIconsLeft = [];
    for (let i of buttons.keys()) {
        const currentRow = buttons[i];

        const buttonName = currentRow[0];
        const onClick = currentRow[1];
        const className = "btn " + currentRow[2];
        const disabled = currentRow[3];
        const icon = currentRow[4];

        materialIconsLeft.push(
            <div key={i} className="col s3">
                <button
                    onClick={onClick}
                    className={className}
                    disabled={disabled}
                >
                    <i className="material-icons left">{icon}</i>
                    {buttonName}
                </button>
            </div>
        );
    }
    return (
        <fieldset className="container valign-wrapper">
            <div className="center container">
                <h4>{getTitle}</h4>
                {rowLeftAlign}
                <div className="row">
                    <div className={!getIsEdit ? "col s3" : "col s6"} />
                    {materialIconsLeft}
                </div>
            </div>
        </fieldset>
    );
};