import { useEffect, useState } from "react";
import Swal from "sweetalert2";
import { errorAlertThenCloseWindow, successAlert } from "../../alert/SweetAlert";
import { Api, formatDecimal, replaceNonNumber, responseHandler, toLocaleString_US } from "../../Api/index";
import { useLocation, useNavigate } from "../../../node_modules/react-router-dom/dist/index";

export const CustomerDetail = () => {
    const location = useLocation();
    const locationState = (location.state || {});
    const id = locationState.id;

    const [getName, setName] = useState("");
    const [getKtp, setKtp] = useState("");
    const [getHp, setHp] = useState("");
    const [getJenisKelamin, setJenisKelamin] = useState("");
    const [getJenisUsaha, setJenisUsaha] = useState("");
    const [getTxnLimit, setTxnLimit] = useState("0");
    const [getCreatedDate, setCreatedDate] = useState();
    const [getCreatedBy, setCreatedBy] = useState();
    const [getUpdatedDate, setUpdatedDate] = useState();
    const [getUpdatedBy, setUpdatedBy] = useState();
    const [getRecStatus, setRecStatus] = useState();
    const [getIsEdit, setIsEdit] = useState(false);
    const [getTitle, setTitle] = useState("Pengaturan Customer - Detail");

    const navigate = useNavigate();

    const onClickEdit = () => {
        setTitle("Pengaturan Customer - Ubah");
        setIsEdit(true)
    };

    const onClickDelete = () => {
        Swal.fire({
            title: "Do you want to delete this customer?",
            showDenyButton: true,
            confirmButtonText: "Yes",
            denyButtonText: "No",
        }).then(async (result) => {
            if (result.isConfirmed) {
                const response = await Api.Customer.doDelete({
                    id: id,  
                    actorId: 0,
                });
                responseHandler(response, () => {
                    successAlert("Customer has been deleted");
                    navigate(-1);
                });
            }
            else if (result.isDenied) {
                Swal.fire("Customer is not removed", "", "info");
            }
        });
    };
    const onClickSave = async () => {
        Swal.fire({
            title: "Do you want to save the changes?",
            showDenyButton: true,
            confirmButtonText: "Yes",
            denyButtonText: "No",
        }).then(async (result) => {
            if (result.isConfirmed) {
                const response = await Api.Customer.doUpdate({
                    id: id,
                    name: getName,
                    ktp: getKtp,
                    hp: getHp,
                    jenisKelamin: getJenisKelamin,
                    idJenisUsaha: getJenisUsaha,
                    txnLimit: getTxnLimit,
                });
                responseHandler(response, () => {
                    successAlert("Customer has been updated");
                });
            }
            else if (result.isDenied) {
                Swal.fire("Changes are not saved", "", "info");
            }
        });
    };
    const getDetail = async () => {
        const response = await Api.Customer.doGetDetail(id);
        responseHandler(response, () => {
            const details = response.data.data;
            setName(details.name);
            setKtp(details.ktp);
            setHp(details.hp);
            setJenisKelamin(details.jenisKelamin);
            setJenisUsaha(details.idJenisUsaha);
            setTxnLimit(details.txnLimit);
            setCreatedDate(details.createdDate);
            setCreatedBy(details.creatorId);
            setUpdatedDate(details.updatedDate);
            setUpdatedBy(details.updaterId);
            setRecStatus(details.recStatus);
        });
    };

    useEffect(() => {
        if (id === undefined) {
            errorAlertThenCloseWindow("Error", "No Customer ID detected!");
        } else {
            getDetail();
        }
    }, []);

    const customerSettings = [
        ["Customer Id",
            true,
            id,
            () => {},
            {}],
        ["Nama Customer",
            !getIsEdit,
            getName,
            (e) => setName(e.target.value),
            {}],
        ["Nomor Ktp",
            !getIsEdit,
            getKtp,
            (e) => setKtp(replaceNonNumber(e.target.value)),
            {}],
        ["Nomor Hp",
            !getIsEdit,
            getHp,
            (e) => setHp(replaceNonNumber(e.target.value)),
            {}],
        ["Jenis Kelamin",
            !getIsEdit,
            getJenisKelamin,
            (e) => setJenisKelamin(e.target.value),
            { type: "radio", name: "jenisKelamin" }],
        ["Jenis Usaha",
            !getIsEdit,
            getJenisUsaha,
            (e) => setJenisUsaha(e.target.value),
            {}],
        ["Maksimal Limit Transaksi Customer",
            !getIsEdit,
            toLocaleString_US(getTxnLimit),
            (e) => setTxnLimit(formatDecimal(e.target.value)),
            {}],
        ["Tanggal Buat / Oleh",
            true,
            getCreatedBy ? `${new Date(getCreatedDate).toLocaleDateString("sv-SE")} / ${getCreatedBy}` : "-",
            () => {},
            {}],
        ["Tanggal Update Terakhir / Oleh",
            true,
            getUpdatedBy ? `${new Date(getUpdatedDate).toLocaleDateString("sv-SE")} / ${getUpdatedBy}` : "-",
            () => {},
            {}],
    ];

    const rowLeftAlign = [];
    for (let i of customerSettings.keys()) {
        const currentRow = customerSettings[i];

        const inputName = currentRow[0];
        const disabled = currentRow[1];
        const value = currentRow[2];
        const onChange = currentRow[3];
        const otherProperties = currentRow[4];

        rowLeftAlign.push(
            <div key={i} className="row left-align">
                <div className="input-field col s12">
                    <h6 className="col s4 pull-s0">{inputName}</h6>
                    {inputName === "Jenis Kelamin" ? (
                        <form>
                            <label>
                                <input
                                    value="P"
                                    checked={getJenisKelamin === "P"}
                                    onChange={onChange}
                                    disabled={disabled}
                                    {...otherProperties}
                                />
                                <span>Pria</span>
                            </label>
                            <label>
                                <input
                                    value="W"
                                    checked={getJenisKelamin === "W"}
                                    onChange={onChange}
                                    disabled={disabled}
                                    {...otherProperties}
                                />
                                <span>Wanita</span>
                            </label>
                        </form>
                    ) : (
                        <input
                            className="col s7"
                            value={value}
                            onChange={onChange}
                            disabled={disabled}
                            {...otherProperties}
                        />
                    )}
                </div>
            </div>
        );
    }

    let buttons = [
        ["Kembali",
            () => navigate(-1),
            "",
            false,
            "arrow_back",
        ],
    ];
    if (!getIsEdit) {
        buttons = buttons.concat([
            ["Hapus",
                onClickDelete,
                "red",
                getRecStatus === "N",
                "delete",
            ],
            ["Ubah",
                onClickEdit,
                "green",
                getRecStatus === "N",
                "edit",
            ],
        ]);
    }
    else {
        buttons = buttons.concat([
            ["Simpan",
                onClickSave,
                "green",
                false,
                "save",
            ],
        ]);
    }
    const materialIconsLeft = [];
    for (let i of buttons.keys()) {
        const currentRow = buttons[i];

        const buttonName = currentRow[0];
        const onClick = currentRow[1];
        const className = "btn " + currentRow[2];
        const disabled = currentRow[3];
        const icon = currentRow[4];

        materialIconsLeft.push(
            <div key={i} className="col s3">
                <button
                    onClick={onClick}
                    className={className}
                    disabled={disabled}
                >
                    <i className="material-icons left">{icon}</i>
                    {buttonName}
                </button>
            </div>
        );
    }
    return (
        <fieldset className="container valign-wrapper">
            <div className="center container">
                <h4>{getTitle}</h4>
                {rowLeftAlign}
                <div className="row">
                    <div className={!getIsEdit ? "col s3" : "col s6"} />
                    {materialIconsLeft}
                </div>
            </div>
        </fieldset>
    );
};