export const InformasiTransaksiForm = (props) => {
    const {
        pelanggan,
        tanggalTransaksi,
        noTransaksi,
        listProductId,
        productName,
        productDesc,

        onChangePelanggan,
        onChangeListProductId,
    } = props;

    const inputs = [
        [
            ["Pelanggan",
                "pelanggan",
                pelanggan,
            ],
            ["Produk Transaksi",
                "productId",
                listProductId,
            ],
        ],
        [
            ["Tanggal Transaksi",
                "tanggalTransaksi",
                tanggalTransaksi,
            ],
            ["Nama Produk",
                "namaProduk",
                productName,
            ],
        ],
        [
            ["Nomor Transaksi",
                "nomorTransaksi",
                noTransaksi,
            ],
            ["Keterangan Produk",
                "keteranganProduk",
                productDesc,
            ],
        ],
    ];
    
    const rows = [];
    for (let i of inputs.keys()) {
        const tempArray = [];
        for (let j of inputs[i].keys()) {
            const currentRow = inputs[i][j];
            const key = i.toString() + j.toString();

            const inputName = currentRow[0];
            const inputId = currentRow[1];
            const value = currentRow[2];
            const className = "col s6";

            if (inputId === "productId") {
                const productOptions = [];
                for (let i of value.keys()) {
                    const id = value[i];
                    productOptions.push(<option key={productOptions.length} value={id}>{id}</option>);
                }
                tempArray.push(
                    <div key={key} className={className}>
                        <h6 className="col s6 pull-s0">{inputName}</h6>
                        <select
                            id={inputId}
                            className="browser-default"
                            onChange={(e)=>{props.onChangeSelection(e.target.value)}}
                            disabled={productOptions.length <= 1}
                        >
                            <option disabled>Choose your product</option>
                            {productOptions}
                        </select>
                    </div>
                );
            } else {
                const properties = {
                    id: inputId,
                    className: className + "  materialize-textarea",
                    value: value,
                    disabled: true,
                };
                tempArray.push(
                    <div key={key} className={className + "  materialize-textarea"}>
                        <h6 className="col s6 pull-s0">{inputName}</h6>
                        {inputId === "keteranganProduk" ? (<textarea {...properties}/>) : (<input {...properties}/>)}
                    </div>
                );
            }
        }
        rows.push(
            <div key={rows.length} className="row left-align">
                {tempArray}
            </div>
        );
    }
    const fieldset = [];
    for (let i of rows.keys()) {
        fieldset.push(
            <div key={i} className="row">
                {rows[i]}
            </div>
        );
    }
    return (
        <>
            <div className="row">
                <h5>Informasi Transaksi</h5>
                <div className="divider black" />
            </div>
            {fieldset}
        </>
    );
};