export const ProductForm = (props) => {
    const {
        id,
        type,
        name,
        ltvBefore,
        ltvAfter,
        biayaJasaPenyimpananBefore,
        biayaJasaPenyimpananAfter,
        recStatus,

        onChangeId,
        onChangeType,
        onChangeName,
        onChangeLtvBefore,
        onChangeLtvAfter,
        onChangeBiayaJasaPenyimpananBefore,
        onChangeBiayaJasaPenyimpananAfter,
        onChangeRecStatus,
        
        onClickSearch,
        onClickEmpty,
        onClickAdd,
    } = props;

    const inputs = [
        [
            ["Tipe produk",
                "col s6 input-field",
                "tipeProduk",
                type,
                onChangeType,
            ],
            ["Status",
                "col s6",
                "status",
                recStatus,
                onChangeRecStatus,
            ],
        ],
        [
            ["Kode Produk",
                "col s6 input-field",
                "idProduk",
                id,
                onChangeId,
            ],
            ["LTV",
                "col s3 input-field",
                "ltvBefore",
                ltvBefore,
                onChangeLtvBefore,
            ],
            ["sampai",
                "col s3 input-field",
                "ltvAfter",
                ltvAfter,
                onChangeLtvAfter,
            ],
        ],
        [
            ["Nama Produk",
                "col s6 input-field",
                "namaProduk",
                name,
                onChangeName,
            ],
            ["Biaya Jasa Penyimpanan",
                "col s3 input-field",
                "biayaJasaPenyimpananBefore",
                biayaJasaPenyimpananBefore,
                onChangeBiayaJasaPenyimpananBefore,
            ],
            ["sampai",
                "col s3 input-field",
                "biayaJasaPenyimpananAfter",
                biayaJasaPenyimpananAfter,
                onChangeBiayaJasaPenyimpananAfter,
            ],
        ],
    ];
    const rows = [];
    for (let i of inputs.keys()) {
        const tempArray = [];
        for (let j of inputs[i].keys()) {
            const currentRow = inputs[i][j];

            const key = i.toString() + j.toString();
            const inputName = currentRow[0];
            const className = currentRow[1];
            const inputId = currentRow[2];
            const value = currentRow[3];
            const onChange = currentRow[4];

            if (currentRow[0] === "Status") {
                tempArray.push(
                    <div key={key} className={className}>
                        <label htmlFor={inputId}>{inputName}</label>
                        <select
                            id={inputId}
                            className="browser-default"
                            value={value}
                            onChange={onChange}
                        >
                            <option disabled>Choose your option</option>
                            <option value="All">All</option>
                            <option value="A">Active</option>
                            <option value="N">Non-Active</option>
                        </select>
                    </div>
                );
            } else {
                tempArray.push(
                    <div key={key} className={className}>
                        <label htmlFor={inputId}>{inputName}</label>
                        <input
                            id={inputId}
                            className="validate"
                            value={value}
                            onChange={onChange}
                            type="text"
                        />
                    </div>
                );
            }
        }
        rows.push(tempArray);
    }

    const buttons = [
        ["Search",
            onClickSearch,
            "",
            "search",
        ],
        ["Kosongkan",
            onClickEmpty,
            "grey",
            "delete",
        ],
        ["Produk Baru",
            onClickAdd,
            "green",
            "add",
        ],
    ];
    const col_s3 = []
    col_s3.push(<div key={col_s3.length} className="col s3" />);
    for (let i of buttons.keys()) {
        const currentRow = buttons[i];

        const name = currentRow[0];
        const properties = {
            onClick: currentRow[1],
            className: "btn " + currentRow[2],
        };
        const icon = currentRow[3];

        col_s3.push(
            <div key={col_s3.length} className="col s3">
                <button {...properties}>
                    <i className="material-icons left">{icon}</i>{name}
                </button>
            </div>
        );
    }
    rows.push(col_s3);

    const fieldset = [];
    for (let i of rows.keys()) {
        fieldset.push(
            <div key={i} className="row">
                {rows[i]}
            </div>
        );
    }
    
    return (
        <fieldset>
            <div className="row">
                <h5>Cari data Produk</h5>
                <div className="divider black" />
            </div>
            {fieldset}
        </fieldset>
    );
};