export const FormInput = (props) => {
    const {
        label = "",
        info = "",
        type = "",
        value = "",
        onChange = () => {},
        unit = "",
    } = props;

    return (
        <div>
            <label>
                {info + (unit ? " (" : "") + unit + (unit ? ")" : "") + " : "}
            </label>
            <input
                type={type}
                name={label}
                value={Number(value).toLocaleString("id-ID")}
                onChange={onChange}
            />
        </div>
    );

    
};