import { SideNav } from "../SideNav/index";
import M from "../../../node_modules/materialize-css/dist/js/materialize.min.js"
import { Link } from "../../../node_modules/react-router-dom/dist/index";
import { PageList } from "../../App/index";

export const NavBar = () => {
    const ul = [];
    for (const key in PageList) {
        ul.push(<li key={ul.length}><Link to={PageList[key]["path"]}>{key}</Link></li>);
    }
    const onClick = () => {
        const elems = document.querySelectorAll('.sidenav');
        M.Sidenav.init(elems);
      };
    return (
        <div>
            <nav>
                <div className="nav-wrapper blue">
                    <Link onClick={onClick} to="#" data-target="slide-out" className="sidenav-trigger">
                        <i className="material-icons">menu</i>
                    </Link>
                    <div className="container">
                        <Link to="/" className="brand-logo">NDS Training</Link>
                        <ul id="nav-mobile" className="right hide-on-med-and-down">
                            {ul}
                        </ul>
                    </div>
                </div>
            </nav>
            <SideNav />
        </div>
    );
};