import Swal from "sweetalert2";


export const successAlert = (title) => {
    Swal.fire({
        icon: "success",
        title,
        showConfirmButton: false,
        timer: 1500
    });
};

export const errorAlert = (title, html) => {
    errorAlertWithImages(title, html, "");
};

export const errorAlertWithImages = (title, html, imageUrl) => {
    Swal.fire({
        icon: "error",
        title,
        html,
        imageUrl,
    });
};

export const errorAlertThenCloseWindow = (title, html) => {
    Swal.fire({
        icon: "error",
        title,
        html,
    }).then(() => {
        window.history.back();
        window.close(); 
    });
};