import { CustomerPageList } from "../../App/index";
import { Link } from "react-router-dom";

export const CustomerTable = (props) => {
    const tbody = props.list.map((customer) => {
        return (
            <tr key={customer.id}>
                <td>{customer.id}</td>
                <td>{customer.ktp}</td>
                <td>{customer.name}</td>
                <td>{customer.idJenisUsaha}</td>
                <td>{customer.hp}</td>
                <td>{customer.recStatus}</td>
                <td>
                    <Link to={CustomerPageList["Detail Pelanggan"]["path"]} state={{ id: customer.id }}>
                        detail
                    </Link>
                </td>
            </tr>
        )
    })
    return (
        <table className="stripped responsive-table">
            <thead>
                <tr>
                    <th>Customer Id</th>
                    <th>KTP</th>
                    <th>Nama</th>
                    <th>Jenis Usaha</th>
                    <th>Nomor HP</th>
                    <th>Status</th>
                    <th>action</th>
                </tr>
            </thead>
            <tbody>
                {tbody}
            </tbody>
        </table>
    );
};