import { useEffect, useState } from "react";
import { useLocation, useNavigate } from "../../../node_modules/react-router-dom/dist/index";
import { Api, responseHandler } from "../../Api/index";
import { InformasiTransaksiForm } from "./InformasiTransaksiForm";
import { DaftarBarangGadaiTable } from "./DaftarBarangGadaiTable";
import { DataKontrakForm } from "./DataKontrakForm";
import { DataTagihanTable } from "./DataTagihanTable";
import { errorAlertThenCloseWindow } from "../../alert/SweetAlert";

export const TransactionDetail = () => {
    const location = useLocation();
    const locationState = (location.state || {});
    const id = locationState.id;

    const [getPelanggan, setPelanggan] = useState("");
    const [getTanggalTransaksi, setTanggalTransaksi] = useState("");
    const [getNoTransaksi, setNoTransaksi] = useState("");
    const [getListProductId, setListProductId] = useState([]);
    const [getProductName, setProductName] = useState("");
    const [getProductDesc, setProductDesc] = useState("");
    const [getDaftarBarangGadai, setDaftarBarangGadai] = useState([]);
    const [getDataTagihanDanPembayaran, setDataTagihanDanPembayaran] = useState([]);

    const [getTotalNilaiTaksiran, setTotalNilaiTaksiran] = useState("");
    const [getLtv, setLtv] = useState("");
    const [getMaksimalNilaiPinjaman, setMaksimalNilaiPinjaman] = useState("");
    const [getNilaiPencarianPelanggan, setNilaiPencarianPelanggan] = useState("");
    const [getBiayaAdminBuka, setBiayaAdminBuka] = useState("");
    const [getDiskonAdminBuka, setDiskonAdminBuka] = useState("");
    const [getBiayaAdminBukaAkhir, setBiayaAdminBukaAkhir] = useState("");
    const [getTotalNilaiPinjaman, setTotalNilaiPinjaman] = useState("");
    const [getTanggalJatuhTempo, setTanggalJatuhTempo] = useState("");
    const [getBiayaJasaPenyimpanan, setBiayaJasaPenyimpanan] = useState("");
    const [getBiayaJasaPenyimpananPeriode, setBiayaJasaPenyimpananPeriode] = useState("");
    const [getBiayaJasaPenyimpananPerPeriode, setBiayaJasaPenyimpananPerPeriode] = useState("");
    const [getTotalBiayaJasaPenyimpanan, setTotalBiayaJasaPenyimpanan] = useState("");
    const [getBiayaAdminTutup, setBiayaAdminTutup] = useState("");
    const [getTotalPengembalian, setTotalPengembalian] = useState("");

    const navigate = useNavigate();

    const getDetail = async () => {

        const response = await Api.Transaction.doGetDetail(id);
        responseHandler(response, () => {
            const details = response.data.data;
            setPelanggan(details.custId);
            setTanggalTransaksi(details.trxDate);
            setNoTransaksi(details.noTransaksi);
            setListProductId([details.productId]);
            setProductName(details.productName);
            setProductDesc(details.productDesc);
            setDaftarBarangGadai(details.daftarBarangGadai);
            setDataTagihanDanPembayaran(details.daftarCicilan);
            hitung(details);
        });
    };
    const hitung = async (details) => {
        details.totalNilaiTaksiran = 0;
        for (let i of details.daftarBarangGadai.keys()) {
            const selectedBarang = details.daftarBarangGadai[i];
            details.totalNilaiTaksiran += (selectedBarang.jlh * selectedBarang.hargaPerSatuan);
        }
        const hitungObject = {
            "productId": details.productId,
            "totalNilaiTaksiran":details.totalNilaiTaksiran,
            "nilaiPencarianPelanggan": details.nilaiPencairanPelanggan,
            "diskonAdmBuka": details.diskonAdmBuka,
            "custId": details.custId,
            "daftarBarangGadai": details.daftarBarangGadai,
        };
        const response2 = await Api.Transaction.doHitung(hitungObject);
        responseHandler(response2, () => {
            const details2 = response2.data.data;
            setTotalNilaiTaksiran(details2.totalNilaiTaksiran);
            setLtv(details2.ltv);
            setMaksimalNilaiPinjaman(details2.maksimalNilaiPinjaman);
            setNilaiPencarianPelanggan(details2.nilaiPencarianPelanggan);
            setBiayaAdminBuka(details2.biayaAdminBuka);
            setDiskonAdminBuka(details2.diskonAdminBuka);
            setBiayaAdminBukaAkhir(details2.biayaAdminBukaAkhir);
            setTotalNilaiPinjaman(details2.totalNilaiPinjaman);
            setTanggalTransaksi(details2.tanggalTransaksi);
            setTanggalJatuhTempo(details2.tanggalJatuhTempo);
            setBiayaJasaPenyimpanan(details2.biayaJasaPenyimpanan);
            setBiayaJasaPenyimpananPeriode(details2.biayaJasaPenyimpananPeriode);
            setBiayaJasaPenyimpananPerPeriode(details2.biayaJasaPenyimpananPerPeriode);
            setTotalBiayaJasaPenyimpanan(details2.totalBiayaJasaPenyimpanan);
            setBiayaAdminTutup(details2.biayaAdminTutup);
            setTotalPengembalian(details2.totalPengembalian);
        });
    };

    useEffect(() => {
        if (id === undefined) {
            errorAlertThenCloseWindow("Error", "No Transaction ID detected!");
        } else {
            getDetail();
        }
    }, []);

    return (
        <fieldset className="container valign-wrapper">
            <div>
                <h4>Transaksi Cicilan Tetap - Detail</h4>
                <fieldset>
                        <InformasiTransaksiForm
                            pelanggan={getPelanggan}
                            tanggalTransaksi={getTanggalTransaksi}
                            noTransaksi={getNoTransaksi}
                            listProductId={getListProductId}
                            productName={getProductName}
                            productDesc={getProductDesc}

                            onChangeSelection={()=> {}}
                        />
                </fieldset>
                <fieldset>
                    <DaftarBarangGadaiTable list={getDaftarBarangGadai} onClickDelete={null} />
                </fieldset>
                <fieldset>
                <DataKontrakForm 
                    totalNilaiTaksiran={getTotalNilaiTaksiran}
                    ltv={getLtv}
                    maksimalNilaiPinjaman={getMaksimalNilaiPinjaman}
                    nilaiPencarianPelanggan={getNilaiPencarianPelanggan}
                    biayaAdminBuka={getBiayaAdminBuka}
                    diskonAdminBuka={getDiskonAdminBuka}
                    biayaAdminBukaAkhir={getBiayaAdminBukaAkhir}
                    totalNilaiPinjaman={getTotalNilaiPinjaman}
                    tanggalTransaksi={getTanggalTransaksi}
                    tanggalJatuhTempo={getTanggalJatuhTempo}
                    biayaJasaPenyimpanan={getBiayaJasaPenyimpanan}
                    biayaJasaPenyimpananPeriode={getBiayaJasaPenyimpananPeriode}
                    biayaJasaPenyimpananPerPeriode={getBiayaJasaPenyimpananPerPeriode}
                    totalBiayaJasaPenyimpanan={getTotalBiayaJasaPenyimpanan}
                    biayaAdminTutup={getBiayaAdminTutup}
                    totalPengembalian={getTotalPengembalian}
                    
                    nilaiPencarianPelanggan_diskonAdminBuka_editable={false}
                />
                </fieldset>
                <fieldset>
                    <DataTagihanTable list={getDataTagihanDanPembayaran} />
                </fieldset>
                <div className="row">
                    <div className="col s3">
                        <button
                            onClick={() => navigate(-1)}
                            className="btn"
                        >
                            <i className="material-icons left">arrow_back</i>
                            Kembali
                        </button>
                    </div>
                </div>
            </div>
        </fieldset>
    );
};