import { useEffect, useState } from "react";
import { CicilanForm } from "./CicilanForm";
import { Api, currentPage, responseHandler } from "../../Api/index";
import { CicilanTable } from "./CicilanTable";
import { Pagination } from "../../components/Pagination/index";
import { CircularPreLoader } from "../../components/CircularPreLoader/index";
import { useNavigate, useSearchParams } from "../../../node_modules/react-router-dom/dist/index";



export const Cicilan = () => {

    const localStorageNoTransaksi = "cicilan.noTransaksi";
    const localStorageIdPelanggan = "cicilan.idPelanggan";
    const localStorageNamaPelanggan = "cicilan.namaPelanggan";
    const localStorageTanggalCicilanAwal = "cicilan.tanggalCicilanAwal";
    const localStorageTanggalCicilanAkhir = "cicilan.tanggalCicilanAkhir";

    const [getNoTransaksi, setNoTransaksi] = useState(localStorage.getItem(localStorageNoTransaksi) || "");
    const [getIdPelanggan, setIdPelanggan] = useState(localStorage.getItem(localStorageIdPelanggan) || "");
    const [getNamaPelanggan, setNamaPelanggan] = useState(localStorage.getItem(localStorageNamaPelanggan) || "");
    const [getTanggalCicilanAwal, setTanggalCicilanAwal] = useState(localStorage.getItem(localStorageTanggalCicilanAwal) || "");
    const [getTanggalCicilanAkhir, setTanggalCicilanAkhir] = useState(localStorage.getItem(localStorageTanggalCicilanAkhir) || "");

    const [getList, setList] = useState([]);
    const [getIsLoading, setIsLoading] = useState(false);
    const [getMaxItems, setMaxItems] = useState(0);
    const [getTotalPages, setTotalPages] = useState(0);
    const [getSearchParameter, setSearchParameter] = useSearchParams();

    const navigate = useNavigate();

    const onClickSearch = async (page) => {
        setIsLoading(true);
        
        const updatedSearchParams = new URLSearchParams(getSearchParameter.toString());
        updatedSearchParams.set('page', page);
        setSearchParameter(updatedSearchParams.toString());

        localStorage.setItem(localStorageNoTransaksi, getNoTransaksi);
        localStorage.setItem(localStorageIdPelanggan, getIdPelanggan);
        localStorage.setItem(localStorageNamaPelanggan, getNamaPelanggan);
        localStorage.setItem(localStorageTanggalCicilanAwal, getTanggalCicilanAwal);
        localStorage.setItem(localStorageTanggalCicilanAkhir, getTanggalCicilanAkhir);

        const search = {
            noTransaksi: getNoTransaksi,
            customerId: getIdPelanggan,
            customerName: getNamaPelanggan,
            cicDateBegin: getTanggalCicilanAwal,
            cicDateEnd: getTanggalCicilanAkhir,
        };

        const response = await Api.Cicilan.doSearch(search, page);
        responseHandler(response, () => {
            setList(response.data.data.results);
            setMaxItems(response.data.data.maxItems);
            setTotalPages(response.data.data.totalPages);
        });
        setIsLoading(false); 
    };

    useEffect(() => {
        onClickSearch(currentPage(getSearchParameter));
    }, []);

    return (
        <div className="container">
            <CicilanForm
                noTransaksi={getNoTransaksi}
                idPelanggan={getIdPelanggan}
                namaPelanggan={getNamaPelanggan}
                tanggalCicilanAwal={getTanggalCicilanAwal}
                tanggalCicilanAkhir={getTanggalCicilanAkhir}

                onChangeNoTransaksi={(e) => setNoTransaksi(e.target.value)}
                onChangeIdPelanggan={(e) => setIdPelanggan(e.target.value)}
                onChangeNamaPelanggan={(e) => setNamaPelanggan(e.target.value)}
                onChangeTanggalCicilanAwal={(e) => setTanggalCicilanAwal(e.target.value)}
                onChangeTanggalCicilanAkhir={(e) => setTanggalCicilanAkhir(e.target.value)}

                onClickSearch={() => {
                    onClickSearch(1);
                }}
                onClickEmpty={() => {
                    setNoTransaksi("");
                    setIdPelanggan("");
                    setNamaPelanggan("");
                    setTanggalCicilanAwal("");
                    setTanggalCicilanAkhir("");
                    localStorage.setItem(localStorageNoTransaksi, "");
                    localStorage.setItem(localStorageIdPelanggan, "");
                    localStorage.setItem(localStorageNamaPelanggan, "");
                    localStorage.setItem(localStorageTanggalCicilanAwal, "");
                    localStorage.setItem(localStorageTanggalCicilanAkhir, "");
                    navigate("?page=1");
                }}
            />
            {getIsLoading ? (
                <div className="valign-wrapper">
                    <CircularPreLoader />
                </div>
            ) : (
                <fieldset>
                    {getList && getList.length > 0 ? <CicilanTable list={getList.slice(0, getMaxItems)} /> : null}
                </fieldset>
            )}
            <Pagination onClick={onClickSearch} page={currentPage(getSearchParameter)} maxItems={getMaxItems} totalPages={getTotalPages}  itemNumbers={getList ? getList.length : 0}/>
        </div>
    );
};