import { CicilanPageList } from "../../App/index";
import { Link } from "../../../node_modules/react-router-dom/dist/index";

export const CicilanTable = (props) => {
    const tbody = props.list.map((cicilan) => {
        return (
            <tr key={cicilan.id}>
                <td>{cicilan.id}</td>
                <td>{cicilan.tanggalTransaksi}</td>
                <td>{cicilan.customerId}</td>
                <td>{cicilan.customerKtp}</td>
                <td>{cicilan.customerName}</td>
                <td>{cicilan.cicilanKe}</td>
                <td>Rp.&nbsp;{(cicilan.pokok + cicilan.bunga + cicilan.denda).toLocaleString("id-ID")}</td>
                <td>{cicilan.status}</td>
                <td>{cicilan.tanggalAktif}</td>
                <td>{cicilan.tanggalJatuhTempo}</td>
                <td>
                    <Link to={CicilanPageList["Detail Cicilan"]["path"]} state={{ id: cicilan.id }}>
                        detail
                    </Link>
                </td>
            </tr>
        )
    })
    return (
        <table className="stripped responsive-table">
            <thead>
                <tr>
                    <th>No Transaksi</th>
                    <th>Tanggal Transaksi</th>
                    <th>ID Pelanggan</th>
                    <th>No KTP</th>
                    <th>Nama Pelanggan</th>
                    <th>Cicilan Ke</th>
                    <th>Total Tagihan</th>
                    <th>Status Cicilan</th>
                    <th>Tanggal Aktif Cicilan</th>
                    <th>Tanggal Jatuh Tempo Cicilan</th>
                    <th>action</th>
                </tr>
            </thead>
            <tbody>
                {tbody}
            </tbody>
        </table>
    );
};