import { TransactionPageList } from "../../App/index";
import { Link } from "../../../node_modules/react-router-dom/dist/index";

export const CicilanDetailForm = (props) => {
    const {
        noPembayaran,
        produkTransaksi,
        pelanggan,
        namaProduk,
        tanggalTransaksi,
        keteranganProduk,
        noTransaksi,
        totalKewajiban,
        totalNilaiPinjaman,
        totalDenda,
        tenor,
        totalPembayaran,
        tanggalJatuhTempo,
        sisaKewajiban,
    } = props;

    const inputs = [
        [
            ["Nomor Pembayaran",
                "noPembayaran",
                noPembayaran,],
            ["Produk Transaksi",
                "produkTransaksi",
                produkTransaksi,],
        ],
        [
            ["Pelanggan",
                "pelanggan",
                pelanggan,],
            ["Nama Produk",
                "namaProduk",
                namaProduk,],
        ],
        [
            ["Tanggal Transaksi",
                "tanggalTransaksi",
                tanggalTransaksi,],
            ["Keterangan Produk",
                "keteranganProduk",
                keteranganProduk,],
        ],
        [
            ["Nomor Transaksi",
                "noTransaksi",
                noTransaksi,],
            ["Total Kewajiban",
                "totalKewajiban",
                totalKewajiban,],
        ],
        [
            ["Total Nilai Pinjaman",
                "totalNilaiPinjaman",
                totalNilaiPinjaman,],
            ["Total Denda",
                "totalDenda",
                totalDenda,],
        ],
        [
            ["Tenor",
                "tenor",
                tenor,],
            ["Total Pembayaran",
                "totalPembayaran",
                totalPembayaran,],
        ],
        [
            ["Tanggal Jatuh Tempo",
                "tanggalJatuhTempo",
                tanggalJatuhTempo,],
            ["Sisa Kewajiban",
                "sisaKewajiban",
                sisaKewajiban,],
        ],
    ];
    
    const rows = [];
    for (let i of inputs.keys()) {
        const tempArray = [];
        for (let j of inputs[i].keys()) {
            const currentRow = inputs[i][j];
            const key = i.toString() + j.toString();

            const inputName = currentRow[0];
            const inputId = currentRow[1];
            const value = currentRow[2];
            const className = "col s6";

            const properties = {
                id: inputId,
                className: className + " materialize-textarea",
                value: value,
                disabled: true,
            };
            tempArray.push(
                <div key={key} className={properties.className}>
                    <h6 className={className + " pull-s0"}>{inputName}</h6>
                    {inputId === "keteranganProduk" ? (<textarea {...properties}/>) : (<input {...properties}/>)}
                </div>
            );
        }
        rows.push(
            <div key={rows.length} className="row">
                <div key={rows.length} className="row left-align">
                    {tempArray}
                </div>
            </div>
        );
    }
    return (
        <>
            <div className="row">
                <h5>Informasi Transaksi</h5>
                <div className="divider black" />
            </div>
            {rows}
            <div className="row left">
                <Link to={TransactionPageList["Detail Transaksi"]["path"]} state={{ id: noTransaksi }}>
                    <button className="btn">
                        Detail Transaksi
                    </button>
                </Link>
            </div>
        </>
    );
};