import { useEffect, useState } from "react";
import { CustomerForm } from "./CustomerForm";
import { useNavigate, useSearchParams } from "react-router-dom";
import { Api, currentPage, responseHandler } from "../../Api";
import { CircularPreLoader } from "../../components/CircularPreLoader";
import { CustomerTable } from "./CustomerTable";
import { Pagination } from "../../components/Pagination";
import { CustomerPageList } from "../../App"

export const Customer = () => {

    const localStorageId = "customer.id";
    const localStorageKtp = "customer.ktp";
    const localStorageName = "customer.name";
    const localStorageHp = "customer.hp";
    const localStorageJenisUsaha = "customer.jenisUsaha";
    const localStorageRecStatus = "customer.recStatus";

    const [getId, setId] = useState(localStorage.getItem(localStorageId) || "");
    const [getKtp, setKtp] = useState(localStorage.getItem(localStorageKtp) || "");
    const [getName, setName] = useState(localStorage.getItem(localStorageName) || "");
    const [getHp, setHp] = useState(localStorage.getItem(localStorageHp) || "");
    const [getJenisUsaha, setJenisUsaha] = useState(localStorage.getItem(localStorageJenisUsaha) || "");
    const [getRecStatus, setRecStatus] = useState(localStorage.getItem(localStorageRecStatus) || "");

    const [getList, setList] = useState([]);
    const [getIsLoading, setIsLoading] = useState(false);
    const [getMaxItems, setMaxItems] = useState(0);
    const [getTotalPages, setTotalPages] = useState(0);
    const [getSearchParameter, setSearchParameter] = useSearchParams();

    const navigate = useNavigate();

    const onClickSearch = async (page) => {
        setIsLoading(true);
        
        const updatedSearchParams = new URLSearchParams(getSearchParameter.toString());
        updatedSearchParams.set('page', page);
        setSearchParameter(updatedSearchParams.toString());
        
        localStorage.setItem(localStorageId, getId);
        localStorage.setItem(localStorageKtp, getKtp);
        localStorage.setItem(localStorageName, getName);
        localStorage.setItem(localStorageHp, getHp);
        localStorage.setItem(localStorageJenisUsaha, getJenisUsaha);
        localStorage.setItem(localStorageRecStatus, getRecStatus);

        const search = {
            id: getId,
            name: getName,
            ktp: getKtp,
            hp: getHp,
            idJenisUsaha: getJenisUsaha,
            recStatus: getRecStatus,
        };

        const response = await Api.Customer.doSearch(search, page);
        responseHandler(response, () => {
            setList(response.data.data.results);
            setMaxItems(response.data.data.maxItems);
            setTotalPages(response.data.data.totalPages);
        });

        setIsLoading(false); 
    };

    useEffect(() => {
        onClickSearch(currentPage(getSearchParameter));
    }, []);

    return (
        <div className="container">
            <CustomerForm
                id={getId}
                ktp={getKtp}
                name={getName}
                hp={getHp}
                jenisUsaha={getJenisUsaha}
                recStatus={getRecStatus}

                onChangeId={(e) => setId(e.target.value)}
                onChangeKtp={(e) => setKtp(e.target.value)}
                onChangeName={(e) => setName(e.target.value)}
                onChangeHp={(e) => setHp(e.target.value)}
                onChangeJenisUsaha={(e) => setJenisUsaha(e.target.value)}
                onChangeRecStatus={(e) => setRecStatus(e.target.value)}

                onClickSearch={() => {
                    onClickSearch(1);
                }}
                onClickEmpty={() => {
                    setId("");
                    setKtp("");
                    setName("");
                    setHp("");
                    setJenisUsaha("");
                    setRecStatus("");
                    localStorage.setItem(localStorageId, "");
                    localStorage.setItem(localStorageKtp, "");
                    localStorage.setItem(localStorageName, "");
                    localStorage.setItem(localStorageHp, "");
                    localStorage.setItem(localStorageJenisUsaha, "");
                    localStorage.setItem(localStorageRecStatus, "");
                    navigate("?page=1");
                }}
                onClickAdd={() => navigate(CustomerPageList["Pelanggan Baru"]["path"])}
            />
            {getIsLoading ? (
                <div className="valign-wrapper">
                    <CircularPreLoader />
                </div>
            ) : (
                <fieldset>
                    {getList && getList.length > 0 ? <CustomerTable list={getList.slice(0, getMaxItems)} /> : null}
                </fieldset>
            )}
            <Pagination onClick={onClickSearch} page={currentPage(getSearchParameter)} maxItems={getMaxItems} totalPages={getTotalPages}  itemNumbers={getList ? getList.length : 0}/>
        </div>
    );
};