import { errorAlert } from "alert/SweetAlert";
import axios from "axios";

const configJson = async (data) => {
    try {
        const response = await axios.get(window.location.origin + "/config.json");
        const json = response.data;
        if ([undefined, null].includes(json[data])) {
            errorAlert("Cannot find key " + data + " on config.json!");
        }
        return json[data];
    }
    catch (e) {
        errorAlert("Failed to load config.json!");
    }
};

export const serverUrl = async () => {return await configJson("serverUrl")};