export const CustomerForm = (props) => {
    const {
        id,
        ktp,
        name,
        hp,
        jenisUsaha,
        recStatus,

        onChangeId,
        onChangeKtp,
        onChangeName,
        onChangeHp,
        onChangeJenisUsaha,
        onChangeRecStatus,
        
        onClickSearch,
        onClickEmpty,
        onClickAdd,
    } = props;

    const inputs = [
        [
            ["No Ktp",
                "col s6 input-field",
                "noKtp",
                ktp,
                onChangeKtp,
            ],
            ["Status",
                "col s6",
                "status",
                recStatus,
                onChangeRecStatus,
            ],
        ],
        [
            ["Nama Pelanggan",
                "col s6 input-field",
                "customerName",
                name,
                onChangeName,
            ],
            ["Nomor Hp",
                "col s6 input-field",
                "nomorHp",
                hp,
                onChangeHp,
            ],
        ],
        [
            ["Id Pelanggan",
                "col s6 input-field",
                "customerId",
                id,
                onChangeId,
            ],
            ["Jenis Usaha",
                "col s6 input-field",
                "jenisUsaha",
                jenisUsaha,
                onChangeJenisUsaha,
            ],
        ],
    ];
    const rows = [];
    for (let i of inputs.keys()) {
        const tempArray = [];
        for (let j of inputs[i].keys()) {
            const currentRow = inputs[i][j];

            const key = i.toString() + j.toString();
            const inputName = currentRow[0];
            const className = currentRow[1];
            const inputId = currentRow[2];
            const value = currentRow[3];
            const onChange = currentRow[4];

            if (currentRow[0] === "Status") {
                tempArray.push(
                    <div key={key} className={className}>
                        <label htmlFor={inputId}>{inputName}</label>
                        <select
                            id={inputId}
                            className="browser-default"
                            value={value}
                            onChange={onChange}
                        >
                            <option disabled>Choose your option</option>
                            <option value="All">All</option>
                            <option value="A">Active</option>
                            <option value="N">Non-Active</option>
                        </select>
                    </div>
                );
            } else {
                tempArray.push(
                    <div key={key} className={className}>
                        <label htmlFor={inputId}>{inputName}</label>
                        <input
                            id={inputId}
                            className="validate"
                            value={value}
                            onChange={onChange}
                            type="text"
                        />
                    </div>
                );
            }
        }
        rows.push(tempArray);
    }

    const buttons = [
        ["Search",
            "",
            onClickSearch,
            "search",
        ],
        ["Kosongkan",
            "grey",
            onClickEmpty,
            "delete",
        ],
        ["Pelanggan Baru",
            "green",
            onClickAdd,
            "add",
        ],
    ];
    const col_s3 = []
    col_s3.push(<div key={col_s3.length} className="col s3" />);
    for (let i of buttons.keys()) {
        const currentRow = buttons[i];
        const buttonName = currentRow[0];
        const className = "btn " + currentRow[1];
        const onClick = currentRow[2];
        const icon = currentRow[3];
        col_s3.push(
            <div key={col_s3.length} className="col s3">
                <button className={className} onClick={onClick}>
                    <i className="material-icons left">{icon}</i>{buttonName}
                </button>
            </div>
        );
    }
    rows.push(col_s3);

    const fieldset = [];
    for (let i of rows.keys()) {
        fieldset.push(
            <div key={i} className="row">
                {rows[i]}
            </div>
        );
    }
    
    return (
        <fieldset>
            <div className="row">
                <h5>Cari data Pelanggan</h5>
                <div className="divider black" />
            </div>
            {fieldset}
        </fieldset>
    );
};