

export const CicilanForm = (props) => {
    const {
        noTransaksi,
        idPelanggan,
        namaPelanggan,
        tanggalCicilanAwal,
        tanggalCicilanAkhir,

        onChangeNoTransaksi,
        onChangeIdPelanggan,
        onChangeNamaPelanggan,
        onChangeTanggalCicilanAwal,
        onChangeTanggalCicilanAkhir,
        
        onClickSearch,
        onClickEmpty,
    } = props;


    const inputs = [
        [
            ["No Transaksi",
                "col s6 input-field",
                "noTransaksi",
                noTransaksi,
                onChangeNoTransaksi,
            ],
        ],
        [
            ["ID Pelanggan",
                "col s6 input-field",
                "idPelanggan",
                idPelanggan,
                onChangeIdPelanggan,
            ],
        ],
        [
            ["Nama Pelanggan",
                "col s6 input-field",
                "namaPelanggan",
                namaPelanggan,
                onChangeNamaPelanggan,
            ],
        ],
        [
            ["Tanggal Cicilan",
                "col s6 input-field",
                "tanggalCicilanAwal",
                tanggalCicilanAwal,
                onChangeTanggalCicilanAwal,
            ],
            ["s/d",
                "col s6 input-field",
                "tanggalCicilanAkhir",
                tanggalCicilanAkhir,
                onChangeTanggalCicilanAkhir,
            ],
        ],
    ];

    const rows = [];
    for (let i of inputs.keys()) {
        const tempArray = [];
        for (let j of inputs[i].keys()) {
            const currentRow = inputs[i][j];
            const key = i.toString() + j.toString();

            const inputName = currentRow[0];
            const className = currentRow[1];
            const inputId = currentRow[2];
            const value = currentRow[3];
            const onChange = currentRow[4];
            let type = "text";
            if (["tanggalCicilanAkhir", "tanggalCicilanAwal"].includes(inputId)) {
                type = "date";
            }
            tempArray.push(
                <div key={key} className={className}>
                    <label htmlFor={inputId}>{inputName}</label>
                    <input
                        id={inputId}
                        className="validate"
                        value={value}
                        onChange={onChange}
                        type={type}
                    />
                </div>
            );
        }
        rows.push(tempArray);
    }

    const buttons = [
        ["Search",
            onClickSearch,
            "",
            "search",
        ],
        ["Kosongkan",
            onClickEmpty,
            "grey",
            "delete",
        ],
    ];
    const col_s3 = []
    col_s3.push(<div key={col_s3.length} className="col s3" />);
    for (let i of buttons.keys()) {
        const currentRow = buttons[i];

        const name = currentRow[0];
        const properties = {
            onClick: currentRow[1],
            className: "btn " + currentRow[2],
        };
        const icon = currentRow[3]

        col_s3.push(
            <div key={col_s3.length} className="col s3">
                <button {...properties}>
                    <i className="material-icons left">{icon}</i>{name}
                </button>
            </div>
        );
    }
    rows.push(col_s3);

    const fieldset = [];
    for (let i of rows.keys()) {
        fieldset.push(
            <div key={i} className="row">
                {rows[i]}
            </div>
        );
    }
    
    return (
        <fieldset>
            <div className="row">
                <h5>Pembayaran Cicilan Tetap</h5>
                <div className="divider black" />
            </div>
            {fieldset}
        </fieldset>
    );
};