import { useEffect, useState } from "react";
import { ProductForm } from "./ProductForm";
import { useNavigate, useSearchParams } from "react-router-dom";
import { Api, currentPage, responseHandler } from "../../Api";
import { CircularPreLoader } from "../../components/CircularPreLoader";
import { ProductTable } from "./ProductTable";
import { Pagination } from "../../components/Pagination";
import { ProductPageList } from "../../App";

export const Product = () => {

    const localStorageId = "product.id";
    const localStorageType = "product.type";
    const localStorageName = "product.name";
    const localStorageLtvBefore = "product.ltvBefore";
    const localStorageLtvAfter = "product.ltvAfter";
    const localStorageBiayaJasaPenyimpananBefore = "product.biayaJasaPenyimpananBefore";
    const localStorageBiayaJasaPenyimpananAfter = "product.biayaJasaPenyimpananAfter";
    const localStorageRecStatus = "product.recStatus";

    const [getId, setId] = useState(localStorage.getItem(localStorageId) || "");
    const [getType, setType] = useState(localStorage.getItem(localStorageType) || "");
    const [getName, setName] = useState(localStorage.getItem(localStorageName) || "");
    const [getLtvBefore, setLtvBefore] = useState(localStorage.getItem(localStorageLtvBefore) || "");
    const [getLtvAfter, setLtvAfter] = useState(localStorage.getItem(localStorageLtvAfter) || "");
    const [getBiayaJasaPenyimpananBefore, setBiayaJasaPenyimpananBefore] = useState(localStorage.getItem(localStorageBiayaJasaPenyimpananBefore) || "");
    const [getBiayaJasaPenyimpananAfter, setBiayaJasaPenyimpananAfter] = useState(localStorage.getItem(localStorageBiayaJasaPenyimpananAfter) || "");
    const [getRecStatus, setRecStatus] = useState(localStorage.getItem(localStorageRecStatus) || "");

    const [getList, setList] = useState([]);
    const [getIsLoading, setIsLoading] = useState(false);
    const [getMaxItems, setMaxItems] = useState(0);
    const [getTotalPages, setTotalPages] = useState(0);
    const [getSearchParameter, setSearchParameter] = useSearchParams();

    const navigate = useNavigate();

    const onClickSearch = async (page) => {
        setIsLoading(true);
        
        const updatedSearchParams = new URLSearchParams(getSearchParameter.toString());
        updatedSearchParams.set('page', page);
        setSearchParameter(updatedSearchParams.toString());

        localStorage.setItem(localStorageId, getId);
        localStorage.setItem(localStorageType, getType);
        localStorage.setItem(localStorageName, getName);
        localStorage.setItem(localStorageLtvBefore, getLtvBefore);
        localStorage.setItem(localStorageLtvAfter, getLtvAfter);
        localStorage.setItem(localStorageBiayaJasaPenyimpananBefore, getBiayaJasaPenyimpananBefore);
        localStorage.setItem(localStorageBiayaJasaPenyimpananAfter, getBiayaJasaPenyimpananAfter);
        localStorage.setItem(localStorageRecStatus, getRecStatus);

        const search = {
            id: getId,
            type: getType,
            name: getName,
            ltvBefore: getLtvBefore,
            ltvAfter: getLtvAfter,
            biayaJasaPenyimpananBefore: getBiayaJasaPenyimpananBefore,
            biayaJasaPenyimpananAfter: getBiayaJasaPenyimpananAfter,
            recStatus: getRecStatus,
        };

        const response = await Api.Product.doSearch(search, page);
        responseHandler(response, () => {
            setList(response.data.data.results);
            setMaxItems(response.data.data.maxItems);
            setTotalPages(response.data.data.totalPages);
        });

        setIsLoading(false); 
    };

    useEffect(() => {
        onClickSearch(currentPage(getSearchParameter));
    }, []);

    return (
        <div className="container">
            <ProductForm
                id={getId}
                type={getType}
                name={getName}
                ltvBefore={getLtvBefore}
                ltvAfter={getLtvAfter}
                biayaJasaPenyimpananBefore={getBiayaJasaPenyimpananBefore}
                biayaJasaPenyimpananAfter={getBiayaJasaPenyimpananAfter}
                recStatus={getRecStatus}

                onChangeId={(e) => setId(e.target.value)}
                onChangeType={(e) => setType(e.target.value)}
                onChangeName={(e) => setName(e.target.value)}
                onChangeLtvBefore={(e) => setLtvBefore(e.target.value)}
                onChangeLtvAfter={(e) => setLtvAfter(e.target.value)}
                onChangeBiayaJasaPenyimpananBefore={(e) => setBiayaJasaPenyimpananBefore(e.target.value)}
                onChangeBiayaJasaPenyimpananAfter={(e) => setBiayaJasaPenyimpananAfter(e.target.value)}
                onChangeRecStatus={(e) => setRecStatus(e.target.value)}

                onClickSearch={() => {
                    onClickSearch(1);
                }}
                onClickEmpty={() => {
                    setId("");
                    setType("");
                    setName("");
                    setLtvBefore("");
                    setLtvAfter("");
                    setBiayaJasaPenyimpananBefore("");
                    setBiayaJasaPenyimpananBefore("");
                    setRecStatus("");
                    localStorage.setItem(localStorageId, "");
                    localStorage.setItem(localStorageType, "");
                    localStorage.setItem(localStorageName, "");
                    localStorage.setItem(localStorageLtvBefore, "");
                    localStorage.setItem(localStorageLtvAfter, "");
                    localStorage.setItem(localStorageBiayaJasaPenyimpananBefore, "");
                    localStorage.setItem(localStorageBiayaJasaPenyimpananAfter, "");
                    localStorage.setItem(localStorageRecStatus, "");
                    navigate("?page=1");
                }}
                onClickAdd={() => navigate(ProductPageList["Produk Baru"]["path"])}
            />
            {getIsLoading ? (
                <div className="valign-wrapper">
                    <CircularPreLoader />
                </div>
            ) : (
                <fieldset>
                    {getList && getList.length > 0 ? <ProductTable list={getList.slice(0, getMaxItems)} /> : null}
                </fieldset>
            )}
            <Pagination onClick={onClickSearch} page={currentPage(getSearchParameter)} maxItems={getMaxItems} totalPages={getTotalPages}  itemNumbers={getList ? getList.length : 0}/>
        </div>
    );
};